<template>
  <el-submenu :index="menu.menu_id">
    <template slot="title">
      <i v-if="menu.menu_icon" :class="[menu.menu_icon]" class="iconfont"></i>
      <span slot="title">{{ menu.menu_name }}</span>
    </template>
    <template v-if="menu.children && menu.children.length">
      <template v-for="(level, index) in menu.children">
        <home-menu-group v-if="level.children && level.children.length" :menu="level" :key="index" />
        <el-menu-item v-else :index="level.menu_id" :key="index" @click="dingRoot.menuItemClick(level)">
          <i v-if="level.menu_icon" :class="[level.menu_icon]" class="iconfont"></i>
          <span>{{ level.menu_name }}</span>
        </el-menu-item>
      </template>
    </template>
  </el-submenu>
</template>

<script>
export default {
  name: "HomeMenuGroup",
  inject: ['dingRoot'],
  props: {
    menu: {
      type: Object,
      required: true,
    },
  }
};
</script>
