import { DsfUploadFile, DsfUploadImg, DsfUploadPhoto, DsfCustomerImg, } from "./upload";
//弹出层
import { DsfDialog, DsfDialogIframe } from "../bhc/dialog";
//树类组件 树信息
import { DsfNavTree, DsfTreeInfo, DsfTreeSelector, DsfTree, DsfCheckNode, } from "./tree";
//选项卡类
import { DsfNavTab, DsfTabItem, DsfStepsNav } from "./tabs";
// 系统类
import { DsfUpdateTime, DsfCreateTime, DsfUpdateUserName, DsfCreateUserName, DsfUpdateUserId, DsfCreateUserId, DsfDeletedStatus, DsfOrder, } from "./system";
// 业务类
import { DsfNavMenu, DsfCalendarNav, DsfBanner } from "./nav";
// 数据面板
import { DsfDataPanelPrimary, DsfDataPanelGroup, DsfDataPanelDiscrete, DsfDataPanelOne } from "./dataPanel";
// 流程类
import { DisposeIdea, FlowInfo, FlowProcess, FormCommentBox, ChooseSendNode, ChooseSendTree, DsfFlowAddComment } from "./flow";
// 项目配置
import { DsfProjectConfig } from './projectConfig';
// 设计类
import { UploadDesignImg } from './design';
// 其他类
import { DsfQrcode } from './other'; 
// 地图
import { DsfBaiduMap } from './map';
// 卡片
import {
  DsfPlatformCardBox,
  DsfPlatformCardItemByCourse,
  DsfPlatformCardItemByCollection,
  DsfPlatformCardItemByTeacher,
  DsfPlatformCardItemByTeacherList,
  DsfPlatformCardItemByTrain
} from './card';


const components = [
  DsfPage,
  DsfPanel,
  DsfButton,
  DsfExportSetting,
  dsfCustomCheckbox,
  DsfButtonBar,
  DsfHtml,
  DsfTextbox,
  DsfTextarea,
  DsfUEditor,
  DsfAutoComplete,
  DsfNumeration,
  DsfCurNumber,
  DsfSelect,
  DsfCascader,
  DsfRadioBox,
  DsfCheckBox,
  DsfOuaSelect,
  DsfAreaSelect,
  DsfDataChoice,
  DsfDatePicker,
  DsfDateIntervalPicker,
  DsfTimePicker,
  DsfLabel,
  DsfTitle,
  DsfTitle1,
  DsfTitle2,
  DsfTitle3,
  DsfTitle4,
  DsfTitle5,
  DsfTitle6,
  DsfFlexColumn,
  DsfRow,
  DsfRowHalf,
  DsfCol,
  DsfRow12,
  DsfRow13,
  DsfTable,
  DsfTableCell,
  DsfTableRow,
  DsfSection,
  DsfIframe,
  DsfViewPart,
  DesTableCellSet,
  DsfFlexibleBox,
  DsfVirtualScroll,
  DsfSubTable,
  DsfSubTableColumn,
  DsfSubTableCellContent,
  DsfDataGrid,
  DsfDataGridCellContent,
  DsfDataGridColumn,
  DsfColumnDataFileter,
  DsfCustomOrder,
  DsfStaticList,
  DsfListStatus,
  DsfCustomList,
  DsfDataGridColumnsFilter,
  DsfSuperSearch,
  DsfUploadFile,
  DsfUploadImg,
  DsfUploadPhoto,
  DsfCustomerImg,
  DsfDialog,
  DsfDialogIframe,
  DsfNavTree,
  DsfTreeInfo,
  DsfTreeSelector,
  DsfTree,
  DsfCheckNode,
  DsfNavTab,
  DsfTabItem,
  DsfStepsNav,
  DsfUpdateTime,
  DsfCreateTime,
  DsfUpdateUserName,
  DsfCreateUserName,
  DsfUpdateUserId,
  DsfCreateUserId,
  DsfDeletedStatus,
  DsfOrder,
  DsfNavMenu,
  DsfCalendarNav,
  DsfBanner,
  DsfDataPanelPrimary,
  DsfDataPanelGroup,
  DsfDataPanelDiscrete,
  DsfDataPanelOne,
  DisposeIdea,
  FlowInfo,
  FlowProcess,
  FormCommentBox,
  ChooseSendNode,
  ChooseSendTree,
  DsfFlowAddComment,
  DsfProjectConfig,
  UploadDesignImg,
  DsfQrcode,
  DsfBaiduMap,
  DsfPlatformCardBox,
  DsfPlatformCardItemByCourse,
  DsfPlatformCardItemByCollection,
  DsfPlatformCardItemByTeacher,
  DsfPlatformCardItemByTeacherList,
  DsfPlatformCardItemByTrain,
  DsfCollapse
]
function install(Vue) {
  Vue.config.performance = true;
  //按需引入element-ui组件
  Vue.prototype.$ELEMENT = {
    zIndex: 15000,
  };
  components.forEach((it) => {
    Vue.component(it.name, it);
    if (!window.$components['platform']) {
      window.$components['platform'] = {}
    }
    window.$components['platform'][it.name] = it;
  })

}
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export {
  install
};
export default install