<template>
  <iframe
    ref="iframe"
    :style="{
      'width':'100%',
      'height':'100%',
      'border-style':'none',
      'display':'block'
    }"
    :src="$src"></iframe>
</template>
<script>
export default {
  name: "IframeLoader",
  props: {
    src: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      isMobile: dsf.global.$isMobile
    };
  },
  computed: {
    $src() {
      let src = this.src;
      return src;
    }
  },
  methods: {},
  watch: {
    $src: {
      handler(v, ov) {
        if (this.$refs.iframe) {
          let iframe = this.$refs.iframe;
          if (this.$router.params.state == "replace") {
            iframe.contentWindow.replace(v);
          } else {
            iframe.contentWindow.location.href = v;
          }
        }
      },
      immediate: true
    }
  }
};
</script>