<template>
  <div :style="controlStyle" class="ds-control">
    <div
      class="dsf-home-pending-process"
      :isDesign="isDesign"
      :style="panelStyle"
    >
      <!-- 设计器 -->
      <template v-if="isDesign">
        <DsfVirtualList
          :isDesign="isDesign"
          :styleAttrs="{ hoverBright: true }"
          :rowPadding="[0, 20, 0, 20]"
          :rowMargin="[0, 20, 0, 20]"
        >
          <template v-slot:list-row="scope">
            <div class="dsf-home-pending-process-row" :read="false">
              <div class="dsf-uhppr-normal">
                <!-- {{scope.row.sign}} -->
                <!-- <template v-for="(sign, index) in scope.row.sign">
                <div :key="index" class="dsf-uhppr-tag">{{sign}}</div>
              </template> -->
                <div class="dsf-uhppr-name-box">
                  <div
                    class="dsf-uhppr-name one-line"
                    :title="scope.row && scope.row.title"
                  >
                    标题名称
                  </div>
                  <span
                    class="dsf-uhppr-desc one-line"
                    title="请尽快处理，后交张三疯审核"
                  >
                    描述
                  </span>
                </div>
                <div class="dsf-uhppr-ren one-line" title="发布人">发布人</div>
                <div class="dsf-uhppr-time">2020-10-01</div>
                <div class="dsf-uhppr-operating">
                  <template v-if="$slots['operating']">
                    <slot name="operating" v-bind="scope" />
                  </template>
                  <template v-else>
                    <div
                      class="dsf-uhppr-operating-item"
                      @click="
                        (e) => operatingClick(e, { type: 'gz', ...scope })
                      "
                    >
                      <DsfIcon name="aixin" style="color: #fc6a6a" />
                      <div class="dsf-uhppr-operating-item-text">关注</div>
                    </div>
                    <div
                      class="dsf-uhppr-operating-item"
                      @click="
                        (e) => operatingClick(e, { type: 'hf', ...scope })
                      "
                    >
                      <DsfIcon name="xiaoxi" style="color: #007ef7" />
                      <div class="dsf-uhppr-operating-item-text">回复</div>
                    </div>
                    <div
                      class="dsf-uhppr-operating-item"
                      @click="
                        (e) => operatingClick(e, { type: 'zd', ...scope })
                      "
                    >
                      <DsfIcon name="zhiding2" style="color: #efaf02" />
                      <div class="dsf-uhppr-operating-item-text">置顶</div>
                    </div>
                    <div
                      class="dsf-uhppr-operating-item"
                      @click="
                        (e) => operatingClick(e, { type: 'th', ...scope })
                      "
                    >
                      <DsfIcon name="chehuisekuai" style="color: #fd7911" />
                      <div class="dsf-uhppr-operating-item-text">退回</div>
                    </div>
                  </template>
                </div>
              </div>
            </div>
          </template>
        </DsfVirtualList>
      </template>
      <template v-else>
        <DsfVirtualList
          :url="url"
          :isDesign="isDesign"
          :styleAttrs="{ hoverBright: true }"
          :rowPadding="[0, 0, 0, 0]"
          :rowMargin="[0, 20, 0, 20]"
          @row-click="rowClick"
        >
          <template v-slot:list-row="scope">
            <slot name="row" v-bind="scope">
              <div
                class="dsf-home-pending-process-row"
                :read="scope.row.status != 0"
              >
                <div class="dsf-uhppr-normal">
                  <!-- <template v-for="(sign, index) in scope.row.sign">
                  <div :key="index" class="dsf-uhppr-tag">{{ sign }}</div>
                </template> -->
                  <div class="dsf-uhppr-name-box">
                    <div
                      class="dsf-uhppr-name one-line"
                      :title="scope.row && scope.row.title"
                    >
                      <img
                        v-if="scope.row.isTop == '1'"
                        class="dsf-uhppr-ontop"
                        :src="
                          require('_platform/assets/styles/themes/img/home/top.png')
                        "
                      />
                      <template v-if="scope.row.signs.length">
                        <template v-for="(sign, index) in scope.row.signs">
                          <img
                            v-if="sign.icon"
                            :key="index"
                            class="dsf-uhppr-ontop"
                            :src="dsf.url.getWebPath(sign.icon)"
                          />
                        </template>
                      </template>
                      {{ scope.row && scope.row.title }}
                    </div>
                    <!-- <span
                    class="dsf-uhppr-desc one-line"
                    title="请尽快处理，后交张三疯审核"
                    >
                    请尽快处理，后交张三疯审核
                  </span> -->
                  </div>
                  <div
                    v-if="scope.row && scope.row.sendUserName"
                    class="dsf-uhppr-ren one-line"
                    :title="scope.row && scope.row.sendUserName"
                  >
                    {{ scope.row && scope.row.sendUserName }}
                  </div>
                  <div class="dsf-uhppr-time">
                    {{ scope.row && scope.row.sendDateTime | time }}
                  </div>
                  <div class="dsf-uhppr-operating">
                    <slot name="operating" v-bind="scope">
                      <div
                        class="dsf-uhppr-operating-item"
                        @click="
                          (e) => operatingClick(e, { type: 'gz', ...scope })
                        "
                      >
                        <template v-if="scope.row.isConcern == 1">
                          <DsfIcon name="aixin_shixin" style="color: #fc6a6a" />
                          <div class="dsf-uhppr-operating-item-text">
                            取消关注
                          </div>
                        </template>
                        <template v-else>
                          <DsfIcon name="aixin" style="color: #fc6a6a" />
                          <div
                            v-if="scope.row.isConcern == 0"
                            class="dsf-uhppr-operating-item-text"
                          >
                            关注
                          </div>
                        </template>
                      </div>
                      <div
                        class="dsf-uhppr-operating-item"
                        @click="
                          (e) => operatingClick(e, { type: 'zd', ...scope })
                        "
                      >
                        <template v-if="scope.row.isTop == 1">
                          <DsfIcon
                            name="zhiding2"
                            style="color: #efaf02; transform: scaleY(-1)"
                          />
                          <div class="dsf-uhppr-operating-item-text">
                            取消置顶
                          </div>
                        </template>
                        <template v-else>
                          <DsfIcon name="zhiding2" style="color: #efaf02" />
                          <div class="dsf-uhppr-operating-item-text">置顶</div>
                        </template>
                      </div>
                      <div
                        class="dsf-uhppr-operating-item"
                        @click="
                          (e) => operatingClick(e, { type: 'cz', ...scope })
                        "
                      >
                        <DsfIcon name="xiaoxi" style="color: #007ef7" />
                        <div class="dsf-uhppr-operating-item-text">操作</div>
                      </div>
                    </slot>
                  </div>
                </div>
              </div>
            </slot>
          </template>
        </DsfVirtualList>
      </template>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfHomePendingProcess",
  ctrlCaption: "首页待办",
  mixins: [$mixins.control],
  filters: {
    time(timeStr) {
      return dsf.date.format(timeStr, "mm-dd");
    },
  },
  props: {
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "100%",
    },
    // 边框
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 列表数据
    list: {
      type: Array,
      default: () => [],
    },
    url: {
      type: String,
      default: () => "",
    },
    viewedUrl: {
      type: String,
      default: "/message/info/markRead",
    },
    toConfig: {
      type: Object,
      default: () => ({}),
    },
    pageSize: {
      type: [String, Number],
      default: 100,
    },
  },

  computed: {
    controlStyle() {
      let { width, padding, height } = this;
      return { width, padding: padding.join("px ") + "px", height: "100%" };
    },
    panelStyle() {
      let { height } = this;
      return { height };
    },
  },

  data() {
    return {};
  },

  watch: {},

  created() {
    if (this.isDesign) {
      this.$dispatch("design-height-change", "100%");
    }
  },

  methods: {
    async rowClick({ args }) {
      let { toConfig, isDesign } = this;
      if (isDesign) return;
      let _this = this;
      let row = args.row || {};
      let title = row.title.replace(/^(.{30}).*/, "$1...") || "";
      let url = row.url;

      const next = async () => {
        if (url) await this.redRow(row._id);
        let openConfig = {
          ...toConfig,
          title,
          url,
          closed: () => {
            if (_this.$homePanel) {
              _this.$homePanel.reloadData();
            } else {
              _this.reloadData();
            }
          },
        };
        if (this.$listeners["itemClick"])
          return this.$dispatch("itemClick", { args, openConfig });
        this.$open(openConfig);
      };
      if (row.type == 0) {
        this.$http
          .get("/wfp/getPageUrl", {
            sID: row.objectId,
            sPID: row.pId,
            sCurLinkID: row.linkId,
            sWFID: row.wfId,
            sNodeID: row.nodeId,
          })
          .then((res) => {
            if (res.data.success && res.data.data) {
              url = res.data.data || "";
              if (url) url += "&isview=1";
              next();
            } else {
              dsf.layer.message(res.data.Message || "请求异常", false);
            }
          })
          .catch((res) => {
            dsf.layer.message("请求异常", false);
          });
      } else {
        next();
      }
    },
    redRow(id) {
      return this.$http.post(this.viewedUrl, {
        id,
        type: "workrecord",
      });
    },
    operatingClick(e, args) {
      e.stopPropagation();
      if (this.$listeners["operatingClick"]) {
        this.$dispatch("operatingClick", args);
      } else {
        if (args.type == "zd") {
          this.ontop(args);
        } else if (args.type == "gz") {
          this.focus(args);
        } else if (args.type == "cz") {
          this.rowClick({ args });
        }
      }
    },
    ontop(args) {
      this.$http
        .post("/message/info/addOrCancelTop", {
          id: args.row._id,
        })
        .then(({ data }) => {
          if (data && data.success) {
            dsf.layer.message("操作成功", true);
            if (this.$homePanel) {
              this.$homePanel.reloadData();
            } else {
              this.reloadData();
            }
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch(() => {
          dsf.layer.message("操作失败", false);
        });
    },
    focus(args) {
      this.$http
        .post("/message/info/addOrCancelConcern", {
          id: args.row._id,
        })
        .then(({ data }) => {
          if (data && data.success) {
            dsf.layer.message("操作成功", true);
            if (this.$homePanel) {
              this.$homePanel.reloadData();
            } else {
              this.reloadData();
            }
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch(() => {
          dsf.layer.message("操作失败", false);
        });
    },
    reloadData() {
      _.each(this.$children, (child) => {
        child?.reloadData && child.reloadData();
      });
    },
  },
});
</script>

