<template>
  <div :style="controlStyle" class="ds-control">
    <div class="ds-announcemen-mail" :style="panelStyle">
      <DsfVirtualScroll>
        <DsfVirtualList
          ref="vlist"
          :isDesign="isDesign"
          :url="dataUrl"
          v-bind="{
            rowPadding,
            rowMargin,
          }"
          @list-change="listChangeFn"
          @row-click="rowClick"
        >
          <template v-if="isDesign" v-slot:list-row>
            <div class="ds-list-col2-row" :style="{ lineHeight }">
              <div
                class="ds-list-col2-item one-line"
                title="标题"
                type="name"
              >
                标题
              </div>
              <div class="ds-list-col2-item" type="time">时间</div>
            </div>
          </template>
          <template v-else v-slot:list-row="scope">
            <div
              class="ds-list-col2-row"
              :read="scope.row.state == 1"
              :style="{ lineHeight }"
            >
              <img
                v-if="scope.row.isTop == 1"
                class="ds-list-col2-item"
                type="top"
                :src="dsf.url.getWebPath('/static/img/home/top.png')"
              />
              <div
                class="ds-list-col2-item one-line"
                :title="scope.row.title || ''"
                type="name"
              >
                {{ scope.row.title || "" }}
              </div>
              <div class="ds-list-col2-item" type="time">
                {{ scope.row.createTime | time }}
              </div>
            </div>
          </template>
        </DsfVirtualList>
      </DsfVirtualScroll>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfUniteHomeAnnouncemenMail",
  ctrlCaption: "首页公告邮件列表",
  mixins: [$mixins.control],
  components: {},
  design: {
    isMask: false,
  },
  filters: {
    time(timeStr) {
      return dsf.date.format(timeStr, "mm-dd");
    },
  },
  props: {
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "100%",
    },
    // 行高
    lineHeight: {
      type: String,
      default: "45px",
    },
    // 边框
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 边框
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 边框
    rowPadding: {
      type: Array,
      default() {
        return [0, 20, 0, 20];
      },
    },
    // 边框
    rowMargin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 列表数据
    list: {
      type: Array,
      default: () => [],
    },
    dataUrl: {
      type: String,
      default: "",
    },
    toConfig: {
      type: Object,
      default: () => ({}),
    },
  },

  computed: {
    controlStyle() {
      let { width, padding, height } = this;
      return { width, padding: padding.join("px ") + "px", height };
    },
    panelStyle() {
      let { height } = this;
      return { height };
    },
  },

  data() {
    return {};
  },

  watch: {},

  created() {},

  methods: {
    reloadData() {
      this.$refs.vlist.reloadData();
    },
    listChangeFn(list) {
      this.$emit("list-change", list);
    },
    rowClick({ args }) {
      let { toConfig } = this;
      if(this.$listeners['row-click']){
        return this.$dispatch("row-click",{args})
      }
      let url = this.$replace(args.row.url, args.row ?? {});
      let title = args.row.title;
      let _this = this;
      this.$open({
        ...toConfig,
        url,
        title,
        closed() {
          if (_this.$homePanel) {
            _this.$homePanel.reloadData();
          } else {
            _this.reloadData();
          }
        },
      });
    },
  },
});
</script>
