<template>
  <div class="ds-super-search-item">
    <label class="ds-super-search-item-text" :title="item.label">{{ item.label }}:</label>
    <div class="ds-super-search-item-block" :class="{'open':item.isExtend}">
      <!--下拉选择，树选择-->
      <template v-if="item.type=='enum'&&(item.showModel=='select'||item.showModel=='treeselect')">
        <DsfSuperSearchSelect :item="item" @click.native.stop :values.sync="item.values" v-if="item.showModel=='select'" :options="item.options||[]" :multipe="item.isAllowMultipe" @focus="focus" @change="change(item)"></DsfSuperSearchSelect>
        <DsfSuperSearchTreeSelect :item="item" @click.native.stop :values.sync="item.values" v-else-if="item.showModel=='treeselect'" :options="item.options||[]" :multipe="item.isAllowMultipe" @focus="focus" @change="change(item)"></DsfSuperSearchTreeSelect>
      </template>
      <!--平铺选项-->
      <template v-else-if="item.type=='enum'&&(item.showModel=='tile' || item.showModel=='treeTile')">
        <div class="ds-super-search-item-option">
          <div class="ds-super-search-item-option-box" @click.stop>
            <el-checkbox-group v-model="item.values">
              <template v-if="item.showModel=='tile'">
                <el-checkbox
                  v-for="op in (item.options||[])"
                  :key="op.value"
                  :label="op.value"
                  @change="checkboxChange(op.value,item,op,false)">
                  {{ op.text }}
                </el-checkbox>
              </template>
              <template v-else-if="item.showModel=='treeTile'">
                <el-checkbox
                  v-for="op in (item.options||[])"
                  :key="op._id"
                  :label="op._id"
                  @change="checkboxTreeChange(op._id,item,op,false)">
                  {{ op._name }}
                </el-checkbox>
              </template>
              <label v-if="item.operator==='between'" class="el-checkbox">
                <el-input v-model="item.customerBetween[0]" class="small"></el-input>
                <span style="margin-left:5px;margin-right:5px">~</span>
                <el-input v-model="item.customerBetween[1]" class="small"></el-input>
                <dsf-button style="margin-top:-2px;min-width:auto;margin-left:5px;" size="small" @click="customerSure(item)">确定</dsf-button>
              </label>
            </el-checkbox-group>
          </div>
          <div v-if="item.isExtend&&item.multiple" style="text-align:center">
            <dsf-button size="small" style="min-width:auto" @click="multipleSure(item)">确定</dsf-button>
            <dsf-button size="small" :type="'plain'" style="min-width:auto" @click="multipleCancel(item)">取消</dsf-button>
          </div>
        </div>

      </template>
      <!--文本框-->
      <template v-if="item.type=='text'">
        <DsfSuperSearchInput :item="item" :values.sync="item.values" :placeholder="item.placeholder||'请输入'+(item.label)" @change="change(item)"></DsfSuperSearchInput>
        <!-- <el-input v-model="item.values" :placeholder="'请输入'+(item.label)" @change="change(item)"></el-input> -->
      </template>
      <!--数字-->
      <template v-if="item.type=='number'">
        <DsfSuperSearchNumber :item="item" :values.sync="item.values" :placeholder="item.placeholder||'请输入'+(item.label)" @change="change(item)"></DsfSuperSearchNumber>
        <!-- <el-input v-model="item.values" :placeholder="'请输入'+(item.label)" @change="change(item)"></el-input> -->
      </template>
      <!--时间-->
      <template v-if="item.type=='datetime'">
        <DsfSuperSearchDataPicker v-if="item.operator==='between'" :values.sync="item.values" :format="item.showModel" :is-range="true" @focus="focus" @change="change(item)"></DsfSuperSearchDataPicker>
        <DsfSuperSearchDataPicker v-else :values.sync="item.values" :format="item.showModel" :is-range="false" @focus="focus" @change="change(item)"></DsfSuperSearchDataPicker>
      </template>
    </div>
    <!--平铺模式的按钮-->
    <div v-if="item.showModel=='tile' || item.showModel=='treeTile'" class="ds-super-search-item-buttons">
      <template v-if="item.isMore&&!item.tileMultipleState">
        <dsf-button v-if="!item.isExtend" style="min-width:auto" type="plain" size="small" icon="shuangjiantouxia" @click.stop="extend(item,true)">
          更多
        </dsf-button>
        <dsf-button v-else style="min-width:auto" type="plain" size="small" icon="shuangjiantoushang" @click.stop="extend(item,false)">
          收起
        </dsf-button>
      </template>
      <dsf-button v-if="item.isAllowMultipe&&!item.tileMultipleState" icon="hao" style="min-width:auto" type="plain" size="small" @click.stop="multipleExtend(item)">
        多选
      </dsf-button>
    </div>
  </div>
</template>
<script>
import DsfSuperSearchSelect from "./superSearchSelect";
import DsfSuperSearchTreeSelect from "./superSearchTreeSelect";
import DsfSuperSearchDataPicker from "./superSearchDataPicker";
import DsfSuperSearchInput from "./superSearchInput.vue";
import DsfSuperSearchNumber from './superSearchNumber.vue'

export default {
  name: "DsfSuperSearchItem",
  components: {
    DsfSuperSearchSelect,
    DsfSuperSearchTreeSelect,
    DsfSuperSearchDataPicker,
    DsfSuperSearchInput,
    DsfSuperSearchNumber
  },
  inject: {
    $superSearch: {
      default: null
    }
  },
  data() {
    return {};
  },
  props: {
    item: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  methods: {
    focus() {
      this.$emit("focus");
    },
    change() {
      this.$emit("change");
    },
    extend(item, mark) {
      item.isExtend = mark;
      this.$superSearch.filterAllClose();
    },
    multipleExtend(item) {
      this.$superSearch.collapseItems();
      item.tileMultipleState = true;
      item.isExtend = true;
      item.values_bak = item.values.slice(0);
      item.multiple = true;
      this.$superSearch.filterAllClose();
      this.$nextTick(() => {
        this.$superSearch.emitHeight();
      });
    },
    multipleSure(item) {
      item.isExtend = false;
      item.multiple = false;
      item.tileMultipleState = false;
      // 如果是树平铺 且 配置了子级字段
      if (item.showModel=='treeTile' && item.childField) {
        let children = [];
        if (item.values.length == 1) {
          let options = _.find(item.options, ({_id}) => _id === item.values[0]);
          children = options.children || [];
        }
        this.setTreeTileChildOptions(item.childField, children);
      }
      this.$superSearch.filterAllClose();
      this.$nextTick(() => {
        this.$superSearch.emitHeight();
        this.change();
      });
    },
    multipleCancel(item) {
      if (item.values_bak) {
        item.values = item.values_bak;
      }
      this.$superSearch.collapseItems();
      this.$superSearch.filterAllClose();
      this.$nextTick(() => {
        this.$superSearch.emitHeight();
      });
    },
    checkboxChange(v, item, option, isFilter) {
      if (!isFilter) {
        // this.$superSearch.filterAllClose();
      }
      if (!item.multiple) {
        if (v) {
          item.values = [option.value];
        } else {
          item.values = [];
        }
        if (item.operator == "between") {
          item.customerBetween = [];
        }
        this.$nextTick(() => {
          this.change();
        });
      }
    },
    checkboxTreeChange(v, item, option, isFilter) {
      if (!isFilter) {
        // this.$superSearch.filterAllClose();
      }
      if (!item.multiple) {
        if (v) {
          item.values = [option._id];
        } else {
          item.values = [];
        }
        if (item.operator == "between") {
          item.customerBetween = [];
        }
        // 如果配置了子级字段
        if (item.childField) {
          let children = option.children || [];
          this.setTreeTileChildOptions(item.childField, children);
        }
        this.$nextTick(() => {
          this.change();
        });
      }
    },
    // 树平铺时为子级字段设置选项
    setTreeTileChildOptions(field, options) {
      if (field === this.item.field) {
        dsf.error(`高级检索：树平铺字段"${this.item.label}"配置的级联子字段名${field}不能与其本身字段名相同`);
        return;
      }
      let childItem = _.find(this.$superSearch.searchItems, it => it.field === field);
      if (!childItem) {
        dsf.error(`高级检索：树平铺字段"${this.item.label}"配置的级联子字段名${field}无效`);
        return;
      }
      if (!childItem.type=='enum' || !childItem.showModel=='treeTile') {
        dsf.error(`高级检索：树平铺字段"${this.item.label}"配置的级联子字段名${field}类型错误`);
        return;
      }
      this.$superSearch.$set(childItem, 'options', options);
      this.$superSearch.$set(childItem, 'values', []);
    },
    customerSure(item) {
      let values = _.filter(item.customerBetween, (v) => v + "");
      if (values.length == 2) {
        item.values = [];
        this.change();
      } else {
        dsf.layer.message("请填写" + item.label + "的区间值", false);
      }
    }
  },
  watch:{
    "item.isExtend":{
      handler(v,ov){
        if(v!=ov){
          this.$nextTick(() => {
            this.$superSearch.emitHeight();
          });
        }
      },
      // immediate:true
    }
  }
};
</script>