<template>
  <div v-show="isDesign || visible" :style="controlStyle" class="ds-control">
    <div
      :class="`ds-home-panel ${styleType ? `ds-home-panel-${styleType}` : ''}`"
      :is-design="isDesign"
      :showShadow="showShadow"
      :style="panelStyle"
    >
      <div class="ds-home-panel-header">
        <div class="ds-home-panel-header-left">
          <template if="!$slots['header-left']">
            <div v-if="titleIcon" class="ds-home-panel-icon">
              <DsfIcon :name="titleIcon"></DsfIcon>
            </div>
            <div v-if="title" class="ds-home-panel-title">{{ title }}</div>
          </template>
          <slot name="header-title"></slot>
          <slot name="header-left"></slot>
        </div>
        <div class="ds-home-panel-header-right">
          <slot name="header-right"></slot>
          <div v-if="moreIcon" class="ds-home-panel-more" @click="more">
            <DsfIcon :name="moreIcon"></DsfIcon>
          </div>
        </div>
      </div>
      <div
        slot-name="default"
        class="ds-home-panel-content"
        :style="{ padding: padding.join('px ') + 'px' }"
      >
        <template v-if="isDesign">
          <slot></slot>
        </template>
        <template v-else>
          <DsfVirtualScroll :isDesign="isDesign">
            <slot></slot>
          </DsfVirtualScroll>
        </template>
      </div>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfHomePanel",
  ctrlCaption: "面板",
  mixins: [$mixins.control],
  provide() {
    return {
      $homePanel: this,
    };
  },
  props: {
    slots: {
      type: Array,
      default() {
        return [{ name: "default", controls: [] }];
      },
    },
    // 标题
    title: {
      type: String,
      default: "面板标题",
    },
    // 标题Icon
    titleIcon: {
      type: String,
      default: () => "qiapianmoshi_kuai",
    },
    // 显示阴影
    showShadow: {
      type: Boolean,
      default: true,
    },
    // 圆角
    borderRadius: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "100%",
    },
    // 更多图标 icon名称
    moreIcon: {
      type: String,
      default: () => "gengduo",
    },
    // 内边框
    padding: {
      type: Array,
      default() {
        return [0, 20, 0, 20];
      },
    },
    // 外边框
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 风格
    styleType: {
      type: String,
      default: "one",
    },
    // 是否可见
    visible: {
      type: Boolean,
      default: true,
    },
    // 更多按钮跳转方式
    moreConfig: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    controlStyle() {
      let { width, margin, height, isDesign, visible } = this;
      let style = {
        width,
        padding: margin.join("px ") + "px",
        height,
      };
      if (isDesign) {
        style.padding = "10px 10px 10px 10px";
        !visible && (style.opacity = "0.5");
      }
      return style;
    },
    panelStyle() {
      let { height, borderRadius } = this;
      let style = {
        borderRadius: borderRadius.join("px ") + "px",
        height,
      };
      let isBorderRadius = borderRadius.some((radius) => radius);
      if (isBorderRadius) style.overflow = "hidden";
      return style;
    },
  },
  data() {
    return {};
  },
  watch: {
    height: {
      immediate: true,
      handler(height) {
        let { isDesign } = this;
        if (isDesign) {
          this.$dispatch("design-height-change", height);
        }
      },
    },
  },
  methods: {
    more() {
      if (this.moreConfig && !this.moreConfig.title) {
        this.moreConfig.title = this.title;
      }
      if (this.$listeners["moreBtn"]) {
        this.$emit("moreBtn");
      }
      this.$open({ ...this.moreConfig }).closed = () => {
        this.reloadData();
      };
    },
    reloadData() {
      this.$childrenReloadData();
    },
  },
  design: {
    isMask: false,
  },
});
</script>
