<template>
  <main class="dsf-dingtalk-home-main">
    <div class="dsf-dingtalk-home-tags">
      <div v-show="showBt" class="dsf-dingtalk-home-tags-bt __left" :disabled="disabledLeft" @click="doPagination(1)">
        <i class="iconfont icon-arrow-left2"></i>
      </div>
      <div ref="box" class="dsf-dingtalk-home-tags-list">
        <draggable v-if="scriptLoaded" v-model="tagList" class="dsf-dingtalk-home-tags-box" v-bind="options">
          <template v-for="tag in tagList">
            <div
              ref="tag"
              :key="tag.menu_id"
              v-context-menu="{params: tag, data: treeMenu }"
              class="dsf-dingtalk-home-tags-item"
              :class="{active: activeTag.menu_id === tag.menu_id}"
              @click="activeTag = tag"
            >
              <i v-if="tag.menu_icon" class="dsf-dingtalk-home-tags-item-icon iconfont" :class="[tag.menu_icon]" ></i>
              <span>{{ tag.menu_name }}</span>
              <i class="dsf-dingtalk-home-tags-item-close iconfont icon-guanbi2" @click.stop="remove({ params: tag })"></i>
            </div>
          </template>
        </draggable>
      </div>
      <div v-show="showBt" class="dsf-dingtalk-home-tags-bt __right" :disabled="disabledRight" @click="doPagination(-1)">
        <i class="iconfont icon-arrow-right2"></i>
      </div>
    </div>
    <div class="dsf-dingtalk-home-body">
      <home-view ref="view" v-for="tag in tagList" v-show="activeTag.menu_id === tag.menu_id" :key="tag.menu_id" :tag="tag" />
    </div>
  </main>
</template>

<script>
import HomeView from "./homeView";

let timer = false, isFirst = true;

export default {
  name: "HomeMain",
  inject: ['homeRoot'],
  components: {
    HomeView
  },
  data() {
    return {
      showBt: true,
      scriptLoaded: false,
      disabledLeft: false,
      disabledRight: false,
      options: {
        animation: 300,
        delay: 200,
        forceFallback: true,
        draggable: ".dsf-dingtalk-home-tags-item",
        ghostClass: "ghost",
        chosenClass: "drag",
      },
      treeMenu: [
        { icon: 'icon-shuaxin', name: '刷新', handler: this.refresh },
        { icon: 'icon-guanbi1', name: '关闭', handler: this.remove },
        { icon: 'icon-guanbi1', name: '关闭其他', handler: this.removeOther },
        { icon: 'icon-delete', name: '关闭全部', handler: this.removeAll },
      ],
      activeTag: null,
      tagList: []
    }
  },
  watch: {
    'homeRoot.menuAction.length': {
      handler(to) {
        if (to && isFirst) {
          isFirst = false;
          if (this._isMounted) {
            this.openTag(this.homeRoot.menuAction[to - 1]);
          } else {
            this._menuAction = this.homeRoot.menuAction[to - 1];
          }
        }
      },
      immediate: true
    }
  },
  created() {
    this.resize = _.throttle(this._resize, 500, {
      leading: true,
      trailing: true
    });
    this.loadScript(() => {
      this.$nextTick(() => this.resize())
    });
  },
  mounted() {
    if (this._menuAction) {
      this.openTag(this._menuAction);
    } else {
      this._isMounted = true;
    }
    window.addEventListener('resize', this.resize);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize);
  },
  methods: {
    loadScript(callback) {
      if (window.vuedraggable) {
        this.scriptLoaded = true;
        callback?.();
      } else {
        this.$http
          .importFiles([
            dsf.url.getWebPath("$/js/libs/vuedraggable/Sortable-1.13.0.js"),
            dsf.url.getWebPath("$/js/libs/vuedraggable/vuedraggable.umd.js"),
          ])
          .then(() => {
            this.scriptLoaded = true;
            callback?.();
          })
          .catch((err) => {
            console.error(err);
            dsf.layer.message("加载fabric文件报错", false);
          });
      }
    },
    _resize() {
      let pageList = this.$el.querySelectorAll(".ds-page");
      pageList.forEach(p => dsf.resizeComponent(p));
      this.updateBtStatus();
    },
    doPagination(type) {
      if ((type === 1 && this.disabledLeft) ||
        (type === -1 && this.disabledRight) || timer) {
        return
      }
      let $box = this.$refs.box;
      let $tagList = this.$refs.tag;
      let bw = $box.offsetWidth;
      let sl = $box.scrollLeft;
      let sR = sl + bw;
      let l = -1;
      if (type === 1) {
        let $tag = $tagList.find(it => it.offsetLeft <= sl && it.offsetWidth + it.offsetLeft > sl);
        if (!$tag) return;
        l = $tag.offsetLeft - (bw - $tag.offsetWidth);
      } else {
        let $tag = $tagList.find(it => it.offsetLeft < sR && it.offsetWidth + it.offsetLeft >= sR);
        if (!$tag) return;
        l = $tag.offsetLeft;
      }
      timer = true;
      $($box).animate({scrollLeft: l}, 300, () => {
        this.updateBtStatus();
        timer = false;
      })
    },
    openTag(item){
      let index = this.tagList.indexOf(item);
      if (index === -1) {
        index = this.tagList.length;
        this.tagList.push(item);
      }
      this.activeTag = item;
      this.$nextTick(() => {
        if (!this.$refs.tag) return;
        let $tag = this.$refs.tag[index];
        let tw = $tag.offsetWidth;
        let tl = $tag.offsetLeft;
        let tr = tw + tl;
        let $box = this.$refs.box;
        let bw = $box.offsetWidth;
        let sl = $box.scrollLeft;
        let sR = sl + bw;
        if (tl >= sl && tr <= sR) {
          return;
        }
        let scrollLeft = tr > sR ? tl - (bw - tw) : tl;
        timer = true;
        $($box).animate({scrollLeft}, 300, () => {
          this.updateBtStatus();
          timer = false;
        })
      });
    },
    refresh({ params }) {
      let index = this.tagList.indexOf(params);
      let $view = this.$refs.view[index];
      $view.refresh();
    },
    remove({ params }) {
      let index = this.tagList.indexOf(params);
      this.tagList.splice(index, 1);
      if (this.activeTag === params) {
        if (!this.tagList.length) {
          this.activeTag = null;
        } else if (index > 0) {
          this.activeTag = this.tagList[index - 1];
        } else {
          this.activeTag = this.tagList[0];
        }
      }
      this.$nextTick(this.updateBtStatus);
    },
    removeOther({ params }) {
      this.activeTag = params;
      this.tagList = [params];
      this.$nextTick(this.updateBtStatus);
    },
    removeAll() {
      this.activeTag = null;
      this.tagList = [];
      this.disabledLeft = true;
      this.disabledRight = true;
    },
    updateBtStatus() {
      let $box = this.$refs.box;
      let bw = $box.offsetWidth;
      let sw = $box.scrollWidth;
      let sl = $box.scrollLeft;
      this.disabledLeft = false;
      this.disabledRight = false;
      if (sl === 0) {
        this.disabledLeft = true;
      }
      if (bw + sl === sw) {
        this.disabledRight = true;
      }
    }
  }
}
</script>