<template>
  <div v-show="isDesign || visible" :style="controlStyle" class="ds-control">
    <div
      :class="`ds-home-tab-panel ${
        styleType ? `ds-home-tab-panel-${styleType}` : ''
      }`"
      :is-design="isDesign"
      :showShadow="showShadow"
      :style="panelStyle"
    >
      <div class="ds-home-panel-header">
        <div class="ds-home-panel-header-left">
          <template if="!$slots['header-left']">
            <div v-if="titleIcon" class="ds-home-panel-icon">
              <DsfIcon :name="titleIcon"></DsfIcon>
            </div>
            <div v-if="title" class="ds-home-panel-title">{{ title }}</div>
          </template>
          <slot name="header-title"></slot>
          <slot name="header-tabs"></slot>
          <div class="ds-home-tab-panel-tabs">
            <template v-for="(item, index) in slots">
              <div
                :key="index"
                class="ds-home-tab-panel-tab"
                :ac="active == item.name"
                @click="tabClick(item)"
              >
                {{ item.title }}
                <div v-if="item.badge" class="ds-home-tab-panel-tab-badge">
                  {{ item.badge }}
                </div>
              </div>
            </template>
          </div>
        </div>
        <div class="ds-home-panel-header-right">
          <slot name="header-right" :active="active"></slot>
          <div
            v-if="getMore && getMore.url && moreIcon"
            class="ds-home-panel-more"
            @click="toMore"
          >
            <DsfIcon :name="moreIcon"></DsfIcon>
          </div>
        </div>
      </div>
      <template v-for="(item, index) in slots">
        <template
          v-if="isDesign || !item.lazy || (item.lazy && active == item.name)"
        >
          <div
            :key="index"
            v-show="active == item.name"
            :slot-name="item.name"
            class="ds-home-panel-content"
            :style="{ padding: padding.join('px ') + 'px' }"
          >
            <template v-if="isDesign">
              <slot :name="item.name"></slot>
            </template>
            <template v-else>
              <DsfVirtualScroll :isDesign="isDesign">
                <slot :name="item.name"></slot>
              </DsfVirtualScroll>
            </template>
          </div>
        </template>
      </template>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfHomeTabPanel",
  ctrlCaption: "标签面板",
  mixins: [$mixins.control],
  provide() {
    return {
      $homePanel: this,
    };
  },
  design: {
    isMask: false,
  },
  props: {
    slots: {
      type: Array,
      default() {
        return [];
      },
    },
    // 标题
    title: {
      type: String,
      default: "面板标题",
    },
    // 标题Icon
    titleIcon: {
      type: String,
      default: () => "qiapianmoshi_kuai",
    },
    // 显示阴影
    showShadow: {
      type: Boolean,
      default: true,
    },
    // 宽度
    width: {
      type: String,
      default: "100%",
    },
    // 高度
    height: {
      type: String,
      default: "300px",
    },
    // 更多图标 icon名称
    moreIcon: {
      type: String,
      default: () => "gengduo",
    },
    // 边框
    padding: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 边框
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    // 风格
    styleType: {
      type: String,
      default: "one",
    },
    // 默认激活
    active: {
      type: String,
      default: "",
    },
    // 是否可见
    visible: {
      type: Boolean,
      default: true,
    },
    badgeUrl: {
      type: String,
      default: "",
    },
    openScroll: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    controlStyle() {
      let { width, margin, height, isDesign, visible } = this;
      let style = {
        width,
        padding: margin.join("px ") + "px",
        height,
      };
      if (isDesign) {
        style.padding = "10px 10px 10px 10px";
        !visible && (style.opacity = "0.5");
      }
      return style;
    },
    panelStyle() {
      let { height } = this;
      return { height };
    },
    getMore() {
      if (this.isDesign) {
        return false;
      }
      let tab = this.getTabObj();
      let more = { url: "" };
      if (tab && tab.more != "") {
        more = tab.more;
      }
      return more;
    },
  },

  data() {
    return {};
  },

  watch: {
    height: {
      immediate: true,
      handler(height) {
        let { isDesign } = this;
        if (isDesign) {
          this.$dispatch("design-height-change", height);
        }
      },
    },
    slots: {
      immediate: true,
      deep: true,
      handler() {
        if (this.slots && this.slots[0]) {
          if (!this.slots.some((slot) => slot.name == this.active)) {
            this.$emit("update:active", this.slots[0].name);
          }
        } else {
          this.$emit("update:active", null);
        }
      },
    },
  },

  created() {
    this.getBadgeUrl();
  },

  methods: {
    // 跳转更多
    toMore() {
      let more = this.getMore;
      if (this.isDesign || !more || !more.url) {
        return false;
      }
      if (this.$listeners["more-click"]) {
        return this.$dispatch("more-click", this.getTabObj());
      }
      this.$open(more);
    },
    tabClick(item) {
      this.$emit("update:active", item.name);
    },
    getTabObj() {
      let { active, slots } = this;
      return slots.find((slot) => slot.name == active);
    },
    getBadgeUrl() {
      let { badgeUrl, isDesign, slots } = this;
      if (isDesign || !badgeUrl) return false;
      let param = {};
      this.$http
        .get(badgeUrl, param)
        .then(({ data }) => {
          if (data.success && data.data) {
            slots.forEach((slot) => {
              this.$set(slot, "badge", data.data[slot.name] || 0);
            });
          } else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch(() => {
          dsf.layer.message("未知错误", false);
        });
    },
    async reloadData() {
      await this.getBadgeUrl();
      this.$childrenReloadData();
    },
  },
});
</script>
