<template>
  <div
    class="ds-control ds-label-box"
    :style="{ height }"
    :class="getCss"
    @dblclick="beginEdit"
  >
    <label
      v-if="!isDesign"
      class="ds_label"
      :class="oneLine ? 'one-line' : ''"
      :title="value"
    >
      {{ value }}
      <slot name="helper">
        <template v-if="dataCapion && dataCapion != ''">
          <el-popover
            v-if="dataCapionType == 'hoverPop' || dataCapionType == 'clickPop'"
            class="ds_label-helper"
            placement="top"
            title=""
            width="200"
            :trigger="dataCapionType == 'hoverPop' ? 'hover' : 'click'"
          >
            <i
              slot="reference"
              class="iconfont icon-bangzhuzhongxin"
              style="font-size: inherit"
            ></i>
            <div v-html="dataCapion"></div>
          </el-popover>
          <i
            v-else
            class="iconfont icon-bangzhuzhongxin ds_label-helper"
            @click="showHelper"
          ></i>
        </template>
      </slot>
    </label>
    <template v-else>
      <label v-if="!editting" class="ds_label">
        {{ value || "无内容" }}
        <slot name="helper">
          <i
            v-if="dataCapion && dataCapion != ''"
            class="iconfont icon-bangzhuzhongxin ds_label-helper"
          ></i>
        </slot>
      </label>
      <div
        v-else
        ref="contenteditable"
        class="contenteditable"
        style="display: block"
        contenteditable
        @blur="endEdit"
        v-text="value"
      ></div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfLabel",
  mixins: [$mixins.text],
  inheritAttrs: false,
  ctrlCaption: "文本",
  props: {
    value: {
      type: [String, Number],
      default: "文本标签",
    },
    textAlign: {
      type: String,
      default: "",
    },
    fontFamily: {
      type: String,
      default: "",
    },
    height: {
      type: String,
      default: "auto",
    },
    oneLine: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      editting: false,
      $required: false,
    };
  },
  computed: {
    getCss() {
      const $class = this.$getClass();
      $class.push(
        this.textAlign,
        "ds_font_" + this.fontFamily,
        this.$required ? "ds_required" : ""
      );
      return $class;
    },
  },
  methods: {
    beginEdit() {
      if (this.isDesign) {
        this.editting = true;
        setTimeout(() => {
          this.$refs.contenteditable.focus();
        }, 0);
      }
    },
    endEdit(e) {
      if (this.isDesign) {
        this.editting = false;
        this.value = e.target.innerHTML;
      }
    },
  },
});
</script>