<template>
  <!-- <div class="control" :style="controlStyle"> -->
  <DsfHomeTabPanel v-bind.sync="daibanPanelConfig" :isDesign="isDesign">
    <template
      v-for="(slot, index) in daibanPanelConfig.slots"
      :slot="slot.name"
    >
      <DsfHomePendingProcess
        :key="index"
        v-bind.sync="daibanPanelContentConfig[slot.name]"
        :toConfig.sync="daibanPanelContentToConfig"
        :isDesign="isDesign"
        :pageSize="pageSize"
      >
        <template slot="operating" slot-scope="scope">
          <slot name="operating" v-bind="scope"></slot>
        </template>
      </DsfHomePendingProcess>
    </template>
  </DsfHomeTabPanel>
  <!-- </div> -->
</template>

<script>
export default dsf.component({
  name: "DsfMessageNotification",
  mixins: [$mixins.control],
  ctrlCaption: "消息待办",
  props: {
    pageSize: {
      type: [Number, String],
      default: 100,
    },
    daibanPanelConfig: {
      default() {
        return {
          slots: [
            {
              title: "全部",
              name: "all",
              hide: false,
              badge: null,
              lazy: false,
              url: "/message/info/getMessageList",
              more: {},
            },
            {
              title: "待办",
              name: "workrecord",
              hide: false,
              badge: null,
              lazy: false,
              url: "/message/info/getMessageList?type=workrecord",
              more: {
                url: "",
              },
            },
            {
              title: "待阅",
              name: "read",
              hide: false,
              badge: null,
              lazy: false,
              url: "/message/info/getMessageList?type=read",
              more: {
                url: "",
              },
            },
            {
              title: "消息",
              name: "tab4",
              hide: false,
              badge: null,
              lazy: false,
              url: "/message/info/getMessageList?type=announcement",
              more: {},
            },
          ],
          title: "",
          titleIcon: "",
          showShadow: true,
          width: "100%",
          height: "100%",
          moreIcon: "gengduo",
          moreUrl: "",
          padding: [0, 0, 25, 0],
          margin: [10, 10, 10, 10],
          styleType: "one",
          active: "all",
          badgeUrl: "/message/info/queryMessageTotal?status=0",
          openScroll: false,
        };
      },
    },
    daibanPanelContentConfig: {
      default() {
        return {
          all: {
            width: "100%",
            height: "100%",
            padding: [0, 0, 0, 0],
            list: [],
            url: "/message/info/getMessageList?pageNum=1",
            toConfig: {},
          },
          workrecord: {
            width: "100%",
            height: "100%",
            padding: [0, 0, 0, 0],
            list: [],
            url: "/message/info/getMessageList?type=workrecord&pageNum=1",
            toConfig: {},
          },
          read: {
            width: "100%",
            height: "100%",
            padding: [0, 0, 0, 0],
            list: [],
            url: "/message/info/getMessageList?type=read&pageNum=1",
            toConfig: {},
          },
          tab4: {
            width: "100%",
            height: "100%",
            padding: [0, 0, 0, 0],
            list: [],
            url: "/message/info/getMessageList?type=announcement&pageNum=1",
            toConfig: {},
          },
        };
      },
    },
    daibanPanelContentToConfig: {
      type: Object,
      default: () => ({}),
    },
  },
  watch: {},
  created() {
    if (!this.isDesign) {
      let daibanPanelContentConfig = { ...this.daibanPanelContentConfig };
      for (let i in daibanPanelContentConfig) {
        daibanPanelContentConfig[i].url =
          daibanPanelContentConfig[i].url + "&pageSize=" + this.pageSize;
      }

      this.$emit("update:daibanPanelContentConfig", daibanPanelContentConfig);
    } else {
      this.$dispatch("design-height-change", "100%");
    }
  },
  computed: {
    controlStyle() {
      let { widthPanel, heightPanel } = this;
      return { width: widthPanel, height: heightPanel };
    },
  },
});
</script>

<style>
</style>