<template>
  <el-select
    class="ds-area-select-item"
    v-model="value"
    :style="{ width: itemWidth }"
    :disabled="disabled"
    :placeholder="placeholder"
    value-key="areacode"
    @change="onChange">
    <el-option v-for="el in (options[pid] || [])" :key="el.areacode" :label="el.name" :value="el"></el-option>
  </el-select>
</template>

<script>
import { gradeConfig, getOptions } from './utils/areaUtils';

export default {
  name: "DsfAreaSelectItem",
  props: {
    isDesign: {
      type: Boolean,
      default: false
    },
    code: {
      type: Number,
      required: true
    },
    isFirst: {
      type: Boolean,
      default: false
    },
    isLast: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      loading: false,
      value: null,
      options: {}
    }
  },
  computed: {
    placeholder() {
      if (this.loading) {
        return "加载中";
      }
      return this.options[this.pid]?.length ? `请选择${gradeConfig[this.code].zhName}` : '无数据';
    },
    prevItem() {
      if (this.isFirst) return null;
      let items = this.$parent.$refs.item;
      if (!items || !items.length) return null;
      return items[this.$parent.range[0] - 1 + this.code];
    },
    nextItem() {
      if (this.isLast) return null;
      let items = this.$parent.$refs.item;
      if (!items || !items.length) return null;
      return items[+this.$parent.range[0] + 1 + this.code];
    },
    pid() {
      if (this.isFirst) {
        return this.$parent.superCode;
      }
      if (this.prevItem?.value) {
        return this.prevItem.value.areacode;
      }
      return '';
    },
    globalId() {
      if (!this.prevItem) {
        return this.pid;
      }
      return this.prevItem.globalId + '.' + this.pid;
    },
    disabled() {
      return this.loading || !this.options[this.pid]?.length;
    }
  },
  watch: {
    'pid': {
      handler(to) {
        if (to) {
          this.getData();
        }
      }
    }
  },
  created() {
    if (this.isFirst) {
      this.getData();
    }
  },
  methods: {
    // 清空值
    clear() {
      this.value = null;
      if (this.nextItem) {
        this.$parent.reset();
        this.nextItem.clear();
        this.nextItem.getData();
      }
    },
    onChange() {
      if (this.nextItem) {
        this.nextItem.clear();
      } else {
        this.$parent.updateValue();
      }
    },
    // 请求数据
    getData() {
      if (this.isDesign || !this.pid) return;
      if (this.options[this.pid]) {
        this.getDataSuccess(this.options[this.pid]);
        return
      }
      let pid = this.pid;
      getOptions.call(this, {
        ids: this.globalId,
        endRange: this.code + 2,
        startRange: this.code + 2
      }, data => {
        this.options[pid] = data;
        if (!data.length) {
          this.$parent.updateValue();
        } else {
          this.getDataSuccess(this.options[this.pid]);
        }
      });
    },
    getDataSuccess(data) {
      if (data?.length) {
        // 默认值复现
        if (this.pid == this.$parent.ids[this.code] && this.$parent.ids.length > this.code + 1) {
          let id = this.$parent.ids[this.code + 1];
          this.value = data.find(d => d.areacode === id);
        }
        // 如果需要自动选中第一级
        else if (this.$parent.autoSelect && !this.isFirst) {
          this.$nextTick(() => {
            this.value = data[0];
            if (this.isLast) {
              this.$nextTick(() => {
                this.$parent.updateValue();
              });
            }
          });
        }
      }
    }
  }
};
</script>