<template>
  <!-- :style="controlStyle" -->
  <DsfHomeTabPanel v-bind.sync="schedulePanelConfig" :isDesign="isDesign">
    <template
      v-for="(slot, indexs) in schedulePanelConfig.slots"
      :slot="slot.name"
    >
      <DsfScheduleListItem
        :url="slot.url"
        :classInfo="slot.name == 'workrecord' ? classInfor : ''"
        :key="indexs"
        :isparameter="slot.name == 'all' ? true : false"
        :is-design="isDesign"
      />
    </template>
  </DsfHomeTabPanel>
</template>

<script>
export default dsf.component({
  name: "DsfScheduleList",
  mixins: [$mixins.control],
  ctrlCaption: "日程班级课表",
  design: {
    isMask: false,
  },
  props: {
    slots: {
      type: Array,
      default() {
        return [];
      },
    },
    schedulePanelConfig: {
      default() {
        return {
          slots: [
            {
              title: "个人日程",
              name: "all",
              hide: false,
              badge: null,
              lazy: false,
              url: "/calendar/info/queryUserCalendarByDate",
              more: {},
            },
            // {
            //   title: "班级课表",
            //   name: "workrecord",
            //   hide: false,
            //   badge: null,
            //   lazy: false,
            //   url: "/teas/schedule/course/possiblecourses/getClassCourseByTime",
            //   more: {},
            // },
            // {
            //   title: "全校课表",
            //   name: "read",
            //   hide: false,
            //   badge: null,
            //   lazy: false,
            //   url: "/teas/schedule/course/possiblecourses/getCourseByTime",
            //   more: {},
            // },
          ],
          title: "",
          titleIcon: "",
          showShadow: true,
          width: "100%",
          height: "100%",
          moreIcon: "gengduo",
          moreUrl: "",
          padding: ["0", "20", "0", "20"],
          margin: ["10", "10", "10", "10"],
          styleType: "one",
          active: "all",
          badgeUrl: "",
          openScroll: false,
        };
      },
    },
  },
  data() {
    return {
      classInfor: {},
      // 当前选中班级的索引
      activeClassIndex: 0,
    };
  },
  computed: {},
  created() {
    if (this.isDesign) {
      this.$dispatch("design-height-change", "100%");
    }
  },
  mounted() {},
  methods: {},
});
</script>
<style lang="scss">
.control {
  height: 500px;
  .dsf-control {
    padding: 8px 20px;
  }
}
.calendar-bts {
  height: 30px;
  line-height: 30px;
  margin: 17px 0;
}
.select-class {
  background-color: #3c8ddb;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  text-align: center;
  padding: 0;
  cursor: pointer;
  .iconfont {
    line-height: 30px;
    color: #fff;
  }
}
</style>
