<template>
  <div class="ds-control ds-section" :class="getCss" :style="getBackgroundStyle">
    <div class="ds-section-main" :style="margin">
      <div slot-name="default" class="ds-section-content" :style="contentStyle">
        <slot name="default"></slot>
      </div>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfSection",
  ctrlCaption: "页面段落",
  mixins: [$mixins.layout],
  data() {
    return {
      paddingSize: 12,
      marginLevel: {
        small: 1,
        big: 8,
        medium: 3
      }
    }
  },
  props: {
    isBlockControl:{
      type:Boolean,
      default:true
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: 'default', controls: [] }
        ]
      }
    },
    shadow: {
      type: Boolean,
      default: false
    },
    showModel: {
      type: String,
      default: 'none'
    },
    marginTop: {
      type: Boolean,
      default: false
    },
    marginBottom: {
      type: Boolean,
      default: false
    },
    marginLeft: {
      type: Boolean,
      default: false
    },
    marginRight: {
      type: Boolean,
      default: false
    },
    paddingTop: {
      type: Boolean,
      default: true
    },
    paddingBottom: {
      type: Boolean,
      default: true
    },
    paddingLeft: {
      type: Boolean,
      default: true
    },
    paddingRight: {
      type: Boolean,
      default: true
    },
    marginArr: {
      type: Array,
      default() {
        return ['0px', '0px', '0px', '0px']
      }
    },
    paddingArr: {
      type: Array,
      default() {
        return ['12px', '0', '12px', '0']
      }
    },
    marginSize: {
      type: String,
      default: "small"
    },
    mainWidth: {
      type: String,
      default: "100%"
    }
  },

  computed: {
    getCss() {
      return [
        ...this.$getClass(),
        this.shadow ? 'shadow' : '',
        this.showModel == 'full' ? 'full' : ''
      ]
    },
    margin(){
      if (this.marginSize === 'custom') {
        return {
          'padding': _.map(this.marginArr, m => {
            return m || '0px';
          }).join(' ')
        }
      }
      let style = {}, m = this.marginLevel[this.marginSize];
      if(this.marginTop){
        style['padding-top'] = m + 'px';
      }
      if(this.marginLeft){
        style['padding-left'] = m + 'px';
      }
      if(this.marginRight){
        style['padding-right'] = m + 'px';
      }
      if(this.marginBottom){
        style['padding-bottom'] = m + 'px';
      }
      return style
    },
    contentStyle() {
      let paddings;
      if (this.marginSize === 'custom') {
        paddings = _.map(this.paddingArr, p => {
          return p || '0px';
        });
      } else {
        paddings = [
          (this.paddingTop ? this.paddingSize : 0) + 'px',
          (this.paddingRight ? this.paddingSize : 0) + 'px',
          (this.paddingBottom ? this.paddingSize : 0) + 'px',
          (this.paddingLeft ? this.paddingSize : 0) + 'px',
        ];
      }
      return {
        'width': this.mainWidth,
        'margin-left': 'auto',
        'margin-right': 'auto',
        'padding': paddings.join(' '),
      };
    }
  },
  updated() {
    if (this.isDesign) {
      this.$dispatch('design-height-change', this.showModel == 'full' ? "100%" : 'auto');
    }
  },
  methods: {
  }
})
</script>