<template>
  <div class="dsf-dingtalk-home-view">
    <iframe v-if="show" ref="iframe" :src="src" frameborder="0" />
  </div>
</template>

<script>
export default {
  name: "HomeView",
  props: {
    tag: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      show: true
    }
  },
  computed: {
    src() {
      let url = this.tag.menu_url;
      if (url.indexOf('#') == 0 && url.indexOf('#/') != 0) {
        url = url.replace('#', '#/');
      }
      if (url.indexOf('#') == 0) {
        url = './page.html' + url;
      }
      return url;
    }
  },
  methods: {
    refresh() {
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
}
</script>