<template>
  <div class="ds-control ds-tree-selector">
    <el-row>
      <el-col :span="showRightBox?15:24">
        <div class="head">
          <el-input v-model="filterText" placeholder="输入关键字进行过滤">
            <i slot="suffix" class="iconfont icon-Magnifier"></i>
          </el-input>
        </div>
        <div class="content">
          <DsfTree
            v-if="slots.length == 1 && !showTab"
            ref="dsfTree"
            tab-index="0"
            :show-check-box="(typeStyle === 'tree' ? '0' : slots[0].showCheckBox)"
            :types="slots[0].types"
            :choose-one-node="slots[0].chooseOneNode"
            :default-open-series="slots[0].defaultOpenSeries"
            :meta-data-type="slots[0].metaDataType"
            :choose-meta-data="slots[0].chooseMetaData"
            :sql-url="slots[0].sqlUrl"
            :async="slots[0].async"
            :show-type="slots[0].showType"
            :show-sub-dept="slots[0].showSubDept"
            :show-level="slots[0].showLevel"
            :is-design="isDesign"
            :node-key="nodeKey"
            @load-data="treeLoadData" />

          <el-tabs v-else-if="slots.length > 1 || (slots.length == 1 && showTab)" v-model="activeName" type="border-card">
            <el-tab-pane v-for="(d, i) in slots" :ref="d.name" :key="d.name" :label="d.title" :name="d.title" :lazy="isDesign ? false : true">
              <DsfTree
                ref="dsfTree" 
                :tab-index="i"
                :show-check-box="((slots.length == 1 && typeStyle === 'tree')? '0': d.showCheckBox)"
                :types="d.types"
                :choose-one-node="d.chooseOneNode"
                :default-open-series="d.defaultOpenSeries"
                :meta-data-type="d.metaDataType"
                :choose-meta-data="d.chooseMetaData"
                :sql-url="d.sqlUrl"
                :async="d.async"
                :show-type="d.showType"
                :show-sub-dept="d.showSubDept"
                :show-level="d.showLevel"
                :is-design="isDesign"
                :node-key="nodeKey" 
                @load-data="treeLoadData" />
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
      <el-col v-if="showRightBox" :span="9">
        <div class="head el-row">
          <el-col v-if="!(slots.length == 1 && typeStyle === 'tree')" :span="12" class="ellipsis">
            已选择
            <b class="chooseNum" v-text="selectedData.length"></b>
          </el-col>
          <el-col :span="slots.length == 1 && typeStyle === 'tree' ? 24 : 12" class="ellipsis">
            <span @click="resetSelect">
              <i class="el-icon-refresh-right"></i>重置
            </span>
          </el-col>
        </div>
        <div class="content">
          <ul v-if="slots.length > 1 ? true : typeStyle === 'list'" class="list" :class="{ canSort: getSort() }">
            <li v-for="(li, i) in selectedData" :key="li._id">
              {{ i + 1 }}. {{ li._name }}
              <span>
                <i v-if="getSort()" class="el-icon-top" title="上移" @click="up(i)"></i>
                <i v-if="getSort()" class="el-icon-bottom" title="下移" @click="down(i)"></i>
                <i class="el-icon-close" title="删除" @click="deleted(li)"></i>
              </span>
            </li>
          </ul>
          <el-tree v-else ref="tree" :props="rightTree.props" :data="rightTree.data" :render-content="renderRightTreeContent" :default-expand-all="true" :node-key="nodeKey">
          </el-tree>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfTreeSelector",
  ctrlCaption: "树选择器",
  mixins: [$mixins.control, $mixins.dataResult],
  props: {
    slots: {
      type: Array,
      default: function () {
        return [];
      }
    },
    showCheckbox: {
      type: Boolean,
      default: false,
      desc: "节点是否可被选择"
    },
    sort: {
      type: Boolean,
      default: false,
      desc: "是否可排序"
    },
    typeStyle: {
      type: String,
      default: "list",
      desc: "展现样式，list列表式、tree树结构"
    },
    maxLength: {
      type: Number,
      default: 0
    },
    showTab: {
      type: Boolean,
      default: true,
      desc: "只有一组数据的时候是否呈现标签样式"
    },
    showRightBox: {
      type: Boolean,
      default: true,
      desc: "是否选中右侧区域"
    },
    nodeKey:{
      type: String,
      default: "_id",
    }
  },
  data() {
    return {
      // 搜索值
      filterText: "",
      // 树对象
      treesObj: [],
      // 树选中的值
      treeSelected: [],
      // 右侧
      selectedData: [],
      activeName: "",
      rightTree: {
        data: [],
        props: {
          children: "children",
          label: "_name",
          key: "_id"
        },
        keysOfNode: {}
      },
      isCheckNode: false,
      // 默认选中值
      selectValueCopy: []
    };
  },
  watch: {
    filterText(val) {
      for (let i in this.treesObj) {
        this.treesObj[i].filter(val);
      }
    }
  },
  created() {
    if (!dsf.isEmptyObject(this.$choiceParams) && _.isArray(this.$choiceParams.slots) && this.$choiceParams.slots.length > 0) {
      this.slots.length = this.$choiceParams.slots.length;
      for (let i in this.$choiceParams.slots) {
        this.slots[i] = this.$choiceParams.slots[i];
      }
    }
    this.setSelectValueCopy();
    this.renderSlots();
    // 树改变右侧选择数据
    this.$on("setSelectedData", this.setSelectedData);
    // 右侧选择数据删除或者重置时，需要同步修改树的选择状态
    this.$on("setTreeObj", this.setTreeObj);
    this.$on("deleteSelectValue", this.deleteSelectValue);
    this.setSelectedData();
  },
  methods: {
    reloadData() {
      this.resetSelect();
      let dsfTree = this.$refs.dsfTree;
      if (dsf.type(dsfTree) === 'array') {
        dsfTree.forEach(t => t.reloadData());
      } else {
        dsfTree.reloadData();
      }
    },
    setSelectValueCopy() {
      let selectValue = this.$vm?.$attrs?.selectValue;
      if (dsf.type(selectValue) == "array") {
        let copy = [];
        selectValue.forEach(({ value, text }) => {
          if (value && text) {
            copy.push({
              _id: value,
              _name: text
            });
          }
        });
        // 默认选中值
        this.selectValueCopy = copy;
      }
    },
    // 赋初始值
    treeLoadData({ target, args }) {
      if (this.selectValueCopy.length && args.length) {
        target.$nextTick(() => {
          let tree = target.$refs.tree;
          _.forEach(this.selectValueCopy,({_id})=>{
            _.each(target.idsHasNodeKeysObj?.[_id] || [],(nodeKey)=>{
              tree.setChecked(nodeKey,true);
            })
          })
        });
      }
    },
    getSort() {
      if (this.$choiceParams && dsf.isDef(this.$choiceParams.sort)) {
        return this.$choiceParams.sort;
      }
      return this.sort;
    },
    getMaxLength() {
      if (this.$choiceParams && dsf.isDef(this.$choiceParams.maxLength)) {
        return this.$choiceParams.maxLength;
      }
      return this.maxLength;
    },
    renderSlots() {
      this.activeName = "";
      this.treesObj = [];
      this.treeSelected = [];
      this.selectedData = [];
      for (let i in this.slots) {
        if (i == 0) {
          this.activeName = this.slots[0].title;
        }
        this.treeSelected.push([]);
      }
    },
    setTreeObj(opts) {
      let treeVm = opts.target;
      this.treesObj.push(treeVm.$refs.tree);
      treeVm.filterText = this.filterText;
    },
    setSelectedData(opts) {
      if (opts) {
        if (this.slots.length == 1 && this.typeStyle === "tree") {
          this.setRightTree(opts);
          return;
        }
        this.treeSelected[opts.args.tabIndex] = opts.args.data;
      }

      let datas = [].concat(...this.treeSelected, this.selectValueCopy);
      datas = _.unionBy(datas, "_id");
      this.selectedData = datas;
    },
    deleteSelectValue(li){
      if(li.args){
        li = li.args
      }
      let index = this.selectValueCopy.findIndex(({ _id }) => _id == li._id);
      if (index > -1) {
        this.selectValueCopy.splice(index, 1);
      }
    },
    deleted(li) {
      // 清除树的勾选状态
      _.each(this.treesObj,(treeObj,i)=>{
        _.each(this.$refs.dsfTree[i].idsHasNodeKeysObj?.[li._id],(nodeKey)=>{
          if(treeObj.getNode(nodeKey)?.isLeaf){
            treeObj.setChecked(nodeKey,false,this.slots[i].chooseOneNode)
          }
        })
        this.treeSelected[i] = treeObj.getCheckedNodes();
      })
      this.deleteSelectValue(li);
      this.setSelectedData();
    },
    up(i) {
      // 上移
      if (i > 0) {
        let curr = this.selectedData[i];
        let currIndex = i;
        dsf.array.remove(this.selectedData, curr);
        this.selectedData.splice(currIndex - 1, 0, curr);
      }
    },
    down(i) {
      // 下移
      if (this.selectedData.length - 1 > i) {
        let curr = this.selectedData[i];
        let currIndex = i;
        dsf.array.remove(this.selectedData, curr);
        this.selectedData.splice(currIndex + 1, 0, curr);
      }
    },
    resetSelect() {
      // 重置
      this.selectedData = [];
      for (let i in this.treesObj) {
        this.treeSelected[i] = [];
        this.treesObj[i].setCheckedKeys([]);
      }
      if(this.slots.length == 1 && this.typeStyle === "tree"){
        this.rightTree.data = [];
      }
    },
    getReturnValue() {
      let datas = [];
      if (this.getMaxLength() * 1 > 0 && this.selectedData.length > this.getMaxLength() * 1) {
        dsf.layer.message("已超出选择数量上限【" + this.getMaxLength() + "】", false);
        return false;
      }

      for (let i in this.selectedData) {
        let obj = {};
        if (this.isCheckNode) {
          obj = {
            value: this.selectedData[i]._id,
            text: this.selectedData[i]._name
          };
        }
        datas.push(dsf.mix(obj, this.selectedData[i]));
      }

      return datas;
    },
    renderRightTreeContent(h, { node, data, store }) {
      return (
        <span class="custom-tree-node">
          <span>{node.label}</span>
          <span style="margin-left:16px">
            <i class="el-icon-close" title="删除" on-click={() => this.removeNode(node, data)}></i>
          </span>
        </span>
      );
    },
    removeNode(node, data) {
      const parent = node.parent;
      const children = parent.data.children || parent.data;
      const index = children.findIndex((d) => d._id === data._id);
      children.splice(index, 1);
      this.treesObj[0].setChecked(data[this.nodeKey])
    },
    setRightTree(opts) {
      let node = opts.args;
      let choiceDatas = dsf.mix([], node.data);
      let rightDatas = [];
      for (let i in choiceDatas) {
        let d = dsf.mix({}, choiceDatas[i]);
        if (this.slots[0].chooseOneNode == "0") {
          d["children"] = [];
        }
        rightDatas.push(d);
      }
      this.rightTreeData(rightDatas);
    },
    rightTreeData(data) {
      for (let i in data) {
        let key = data[i][this.nodeKey];
        // 右侧节点数据
        let leftNode = this.treesObj[0].getNode(key);
        let rightNode = this.$refs.tree.getNode(key);
        if (rightNode) {
          if (this.slots[0].chooseOneNode == "1") {
            this.$refs.tree.updateKeyChildren(key, leftNode.data.children || []);
          }
        } else if (leftNode && leftNode.parent && leftNode.parent.key !== undefined) {
          let parentRightNode = this.$refs.tree.getNode(leftNode.parent.key);
          if (parentRightNode) {
            parentRightNode.data.children.push(data[i]);
          } else {
            let d = dsf.mix({}, leftNode.parent.data);
            d.children = [data[i]];
            this.rightTreeData([d]);
          }
        } else {
          this.rightTree.data = this.rightTree.data.concat(data);
        }
      }
    }
  },
  design: {
    isMask: true, //调试模式为false,调试完后会改为true
    fit: true
  }
});
</script>
