<template>
  <div class="dsf-dingtalk-home full-view">
    <header class="dsf-dingtalk-home-header">
      <div class="dsf-dingtalk-home-header-close-bt" :class="{ closed: isClosed }" @click="isClosed = !isClosed">
        <i class="iconfont icon-left-alignment"></i>
        <i class="iconfont icon-zuojiantou"></i>
      </div>
      <home-header></home-header>
    </header>
    <section class="dsf-dingtalk-home-section">
      <dsf-flexible-box
        :is-closed.sync="isClosed"
        :hidden-bt="true"
        margin="0"
        bar-width="220px"
        @resize="$refs.main.resize()"
      >
        <template #bar>
          <aside class="dsf-dingtalk-home-aside">
            <div class="user-info">
              <div class="user-info-content">
                <div class="user-info-icon" :style="{ 'background-image': `url(${$replace(defaultHeader)})` }">
                  <img v-show="showIcon" :src="headIcon | imgFormat" @error="headLoadError" @load="headLoadSuccess" />
                </div>
                <div class="user-info-name" @click="showDropdown = !showDropdown">
                  <p>{{ dsf.cookies.get("user_name") }}</p>
                  <span>
                    欢迎您！
                    <i v-if="dropdownConfig.length" class="iconfont" :class="[showDropdown ? 'icon-shangjiantou' : 'icon-xiajiantou']" />
                  </span>
                </div>
              </div>
            </div>
            <dsf-virtual-scroll>
              <div v-if="dropdownConfig.length" v-show="showDropdown">
                <el-menu active-text-color="#000">
                  <el-menu-item
                    v-for="(item, index) in dropdownConfig"
                    :key="(item.key || '') + '__' + index"
                    :index="index"
                    @click="$emit('dropdown-click', item)"
                  >
                    <i class="iconfont" :class="[item.icon]"></i>
                    <span>{{ item.name }}</span>
                  </el-menu-item>
                </el-menu>
              </div>
              <home-aside-menu v-show="!showDropdown"></home-aside-menu>
            </dsf-virtual-scroll>
          </aside>
        </template>
        <template #default>
          <home-main ref="main" />
        </template>
      </dsf-flexible-box>
    </section>
  </div>
</template>

<script>
import HomeHeader from "./homeHeader.vue";
import HomeMain from "./homeMain.vue";
import HomeAsideMenu from "./homeAsideMenu.vue";
import utils from "../utils";

export default {
  name: "DsfDingTalkHome",
  provide() {
    return {
      dingRoot: this
    };
  },
  components: {
    HomeHeader,
    HomeMain,
    HomeAsideMenu,
  },
  props: {
    dropdownConfig: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      isClosed: false,
      showDropdown: false,
      showIcon: false,
      headIcon: "",
      defaultHeader: dsf.config.setting_public_user_default_header,
    };
  },
  methods: {
    // 头像加载成功时
    headLoadSuccess() {
      this.showIcon = true;
    },
    // 头像加载失败时
    headLoadError() {
      this.showIcon = false;
    },
    // 点击菜单
    menuItemClick(menu) {
      let item = utils.findMenu(menu);
      if (item) {
        switch (item.opentype) {
        // 对话框打开
        case '3':
          dsf.layer.openDialog({
            title: item.menu_name,
            width: '950px',
            height: '540px',
            params: {
              path: url,
            }
          });
          break;
          // 当前窗口打开
        default:
            this.$refs.main?.openTag(item);
        }
      }
    }
  },
};
</script>
