<template>
  <el-dialog
    ref="dialog"
    :index="index"
    class="ds-dialog"
    :class="[footerAlignClassName,conentBorder?'ds-dialog-content-border':'']"
    v-bind.sync="$attrs"
    @closed="closeHandler"
    v-on="$listeners"
    append-to-body
  >
    <template v-slot:title>
      <slot name="title"></slot>
    </template>
    <template v-slot:default>
      <slot></slot>
    </template>
    <template v-slot:footer>
      <slot name="footer"></slot>
    </template>
  </el-dialog>
</template>
<script>
/**
 * @class DsfDialog
 * @description 对话框
 * @example
 * <DsfDialog>
 *  <template slot="title">我是标题</template>
 *  <template>我是主体部分</template>
 *  <template slot="footer">我是底部</template>
 * </DsfDialog>
 */
export default {
  /**
   * @memberof DsfDialog
   * @name Slots
   * @property {*} title 标题部分
   * @property {*} default 主体内容部分
   * @property {*} footer 底部部分
   */
  name: "DsfDialog",
  inheritAttrs: false,
  provide() {
    return {
      $dialog: this
    };
  },
  props: {
    index: {
      type: String,
      default: ""
    },
    options: {
      type: Object,
      default() {
        return {};
      }
    },
    dialogArgs: {
      type: Object,
      default() {
        return null;
      }
    },
    openerVm: {
      type: Object,
      default() {
        return null;
      }
    },
    // 底部left居左,center居中,right居右
    footerAlign: {
      type: String,
      required: false
    },
    //内容区域是否存在边框线
    conentBorder: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      currentView: null
    };
  },
  computed: {
    footerAlignClassName() {
      let align = this.footerAlign || dsf.config.setting_public_default_dialog_footer_align;
      return align && align != "right" ? "dialog-footer-" + align : "";
    }
  },
  mounted() {},
  created() {
    // debugger
  },
  beforeDestroy() {
    // debugger
  },
  methods: {
    closeHandler() {
      this.$emit("close");
    },
    close() {
      this.$emit("update:visible", false);
    }
  }
};
</script>
