<template>
  <div class="ds-control ds-form-item ds-radio-box" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
      <slot name="error">
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </slot>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :class="{border: border || isButton}" :style="getFormItemBlockMarginLeft()">
        <!--只读状态只显示div-->
        <div v-if="readOnly" class="ds-form-readonly">
          <DsfTextProxy v-model="value" v-bind="_self.$props" :show-label="false"></DsfTextProxy>
        </div>
        <el-checkbox-group v-else-if="isButton" v-model="checkValue" :disabled="disabled" v-bind="options" @change="selectChange">
          <template v-if="isDesign&&!items.length">
            <el-checkbox-button label="1">数据1</el-checkbox-button>
            <el-checkbox-button label="2">数据2</el-checkbox-button>
            <el-checkbox-button label="3">数据3</el-checkbox-button>
            <el-checkbox-button label="4">数据4</el-checkbox-button>
            <el-checkbox-button label="5">数据5</el-checkbox-button>
          </template>
          <template v-else>
            <el-checkbox-button v-for="(item, index) in items" :key="index" :label="item.value" :disabled="disabledItemValues.indexOf(item.value) > -1">
              {{ item.text }}
            </el-checkbox-button>
          </template>
          <el-checkbox-button v-if="isOther && !hasOther" label="else">
            其他
          </el-checkbox-button>
        </el-checkbox-group>
        <el-checkbox-group v-else v-model="checkValue" :disabled="disabled" v-bind="options" @change="selectChange">
          <template v-if="isDesign&&!items.length">
            <el-checkbox :border="border" label="1">数据1</el-checkbox>
            <el-checkbox :border="border" label="2">数据2</el-checkbox>
            <el-checkbox :border="border" label="3">数据3</el-checkbox>
            <el-checkbox :border="border" label="4">数据4</el-checkbox>
            <el-checkbox :border="border" label="5">数据5</el-checkbox>
          </template>
          <template v-else>
            <el-checkbox v-for="(item, index) in items" :key="index" :label="item.value" :border="border" :disabled="disabledItemValues.indexOf(item.value) > -1">
              {{ item.text }}
            </el-checkbox>
          </template>
          <el-checkbox v-if="isOther && !hasOther" label="else">
            其他
          </el-checkbox>
        </el-checkbox-group>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfCheckBox",
  mixins: [$mixins.formControl],
  ctrlCaption: "多选", 
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    value: {
      type: [Array, String, Number, Boolean, Object],
      default() {
        return [];
      }
    },
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    // 需要禁用选项的value值得集合
    disabledItemValues: {
      type: Array,
      default() {
        return [];
      }
    },
    type: {
      validator: function (value) {
        // 0: 普通；1：带边框；2：按钮
        return [0, 1, 2].indexOf(parseInt(value)) !== -1;
      },
      default: 0
    },
    maxChooseNum: {
      type: Number,
      default: -1
    },
    minChooseNum: {
      type: Number,
      default: -1
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false
    },
    formatter: {
      type: String,
      default: "textValue^text"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      }
    }
  },
  data() {
    return {
      valueCopy: []
    };
  },
  computed: {
    checkValue: {
      get() {
        if (dsf.type(this.valueCopy) === "array") {
          return this.valueCopy.map((v) => v.value);
        }
        return [];
      },
      set(v) {
        this.initValue(v);
      }
    },
    itemsValue() {
      return this.items.map((v) => v.value);
    },
    border() {
      return this.type === 1;
    },
    isButton() {
      return this.type === 2;
    },
    // 判断items中是否已经存在其他
    hasOther() {
      return this.items.findIndex((v) => v.value === "else") > -1;
    },
    options() {
      const opt = {};
      if (this.maxChooseNum >= 0) {
        opt.max = this.maxChooseNum;
      }
      if (this.minChooseNum >= 0) {
        opt.min = this.minChooseNum;
      }
      return opt;
    }
  },
  watch: {
    value(to) {
      this.initValue(to);
    }
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    selectChange() {
      this.$dispatch("change", this.value);
      // this.$nextTick(() => {
      //   this.emitFormValidate(this.value);
      // });
    },
    initValue(to) {
      if (!this.readOnly) {
        let nv;
        if (to === void 0 || to === null) {
          nv = [];
        } else if (dsf.type(to) === "array") {
          nv = this.filterValue(to);
        } else {
          nv = this.filterValue([to]);
        }
        if (!this.isSame(nv, this.valueCopy)) {
          this.valueCopy = nv;
          this.updateValue(nv);
        }
      }
    },
    filterValue(value) {
      return value
        .map((v) => {
          if (typeof v === "object" && v.value !== void 0) {
            return this.getObjectByValue(v.value);
          } else if (typeof v === "string" || typeof v === "number" || typeof v === "boolean") {
            return this.getObjectByValue(v);
          }
          return void 0;
        })
        .filter((v) => v !== void 0);
    },
    getObjectByValue(v) {
      for (let i = 0; i < this.items.length; i++) {
        let item = this.items[i];
        if (v === item.value) {
          return {
            text: item.text,
            value: v
          };
        }
      }
      return void 0;
    },
    // 判断两个值是否相同
    isSame(p1, p2) {
      if (dsf.type(p1) === "array" && dsf.type(p2) === "array") {
        if (p1.length !== p2.length) return false;
        for (let i = 0; i < p1.length; i++) {
          let same = false;
          for (let j = 0; j < p2.length; j++) {
            if (p1[i].text === p2[j].text && p1[i].value === p2[j].value) {
              same = true;
              break;
            }
          }
          if (!same) {
            return false;
          }
        }
        return true;
      }
      return p1 === p2;
    },
    updateValue(to) {
      this.emitValueChange(to);
      this.$nextTick(() => {
        this.emitFormValidate(to);
      });
    }
  },
  design: {
    layout: {
      //#开头表示该属性在预览模式下会被保留
      exclude: ["value", "#items"]
    },
    settingMenus:[
      ...($mixins.formControl.design.settingMenus||[]),
      {
        text:"选项设置",
        command:"datasource",
        handler(){
          dsf.designer.triggerDialogPropty('metadata.dataSource')
        }
      }
    ]
  }
});
</script>
