<template>
  <label class="dsf-college-home-header-search" :class="{show: show}">
    <i class="iconfont icon-sousuo" @click="submit"></i>
    <input
      ref="input"
      v-model.trim="keywords"
      :placeholder="placeholder"
      type="text"
      @focus="focus"
      @blur="blur"
      @keypress.enter="doSearch"
    />
  </label>
</template>

<script>
export default {
  name: "HomeSearch",
  props: {
    placeholder: {
      type: String,
      default: '请输入关键词'
    }
  },
  data() {
    return {
      isFocus: false,
      keywords: '',
    }
  },
  computed: {
    show() {
      return this.isFocus || this.keywords;
    }
  },
  methods: {
    submit() {
      if (this.isFocus) {
        this.btClick = true;
        this.doSearch();
      }
    },
    focus() {
      this.isFocus = true;
    },
    blur() {
      setTimeout(() => {
        if (!this.btClick) {
          this.isFocus = false;
        }
        this.btClick = false;
      }, 300);
    },
    doSearch() {
      if (this.keywords) {
        this.$emit("search", this.keywords);
      }
    }
  }
}
</script>