<template>
  <div
    v-show="show"
    class="ds-context-menu"
    :style="rootStyle"
    tabindex="0"
    @blur="blur"
    @contextmenu.prevent.stop
  >
    <dsf-context-menu-group v-if="show" :list="list" />
  </div>
</template>

<script>
import DsfContextMenuGroup from "./ContextMenuGroup";

export default {
  name: "DsfContextMenu",
  components: {
    DsfContextMenuGroup
  },
  provide() {
    return {
      contextMenuRoot: this
    };
  },
  data() {
    return {
      show: false,
      point: {
        x: 0,
        y: 0
      },
      list: []
    }
  },
  computed: {
    rootStyle() {
      let { x, y } = this.point;
      return {
        left: x + 'px',
        top: y + 'px'
      }
    }
  },
  methods: {
    open(point, params, data) {
      this.list = data;
      this._params = params || null;
      this.point = point;
      this.show = true
      this.$nextTick(() => {
        this.$el.focus();
      });
    },
    blur() {
      this.show = false;
    },
    itemClick(menu, e){
      if (menu.children?.length) return;
      menu.handler?.({params: this._params, menu, e});
      this.list = [];
      this.$el.blur();
    }
  }
}
</script>

<style lang="scss">
.ds-context-menu{
  color: #666;
  position: fixed;
  z-index: 1000;
  box-sizing: border-box;
  user-select: none;
  * {
    box-sizing: border-box;
  }
  > .ds-context-menu-group{
    display: flex;
  }
  &-group{
    display: none;
    flex-direction: column;
    min-width: 120px;
    background-color: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1),
      0 2px 4px rgba(0, 0, 0, .12),
      0 0 6px rgba(0, 0, 0, .04);
    border: 1px solid #ddd;
    outline: none !important;
    border-radius: 5px;
    padding: 2px;
    position: absolute;
    &.left {
      right: 100%;
    }
    &.right {
      left: 100%;
    }
    &.top {
      bottom: 0;
    }
    &.bottom {
      top: 0;
    }
  }
  &-item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    border-radius: 4px;
    position: relative;
    &:hover{
      background-color: #f6f7f8;
      z-index: 10;
      > .ds-context-menu-group{
        display: flex;
      }
    }
    .icon-right{
      padding: 0 7px 0 15px;
      font-size: 12px;
      color: #999;
      visibility: hidden;
      &[show]{
        visibility: visible;
      }
    }
    &-content{
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 100%;
      padding-left: 30px;
      position: relative;
    }
    &-icon{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 30px;
      color: #333;
      position: absolute;
      top: 0;
      left: 0;
    }
    &-name{
      flex: auto;
      white-space: nowrap !important;
      color: #000;
    }
  }
}
</style>