<template>
  <div class="ds-control ds-tree">
    <el-tree
      ref="tree"
      class="filter-tree"
      :empty-text="loading ? '数据加载中...' : '暂无数据'"
      :props="defaultProps"
      :default-expand-all="isDesign ? true : false"
      :check-on-click-node="true"
      :lazy="_async && !isDesign"
      :load="lazyLoad"
      :data="data"
      :default-expanded-keys="expandedKeys"
      :highlight-current="showCheckBox == '0' ? true : false"
      :filter-node-method="filterNode"
      :show-checkbox="showCheckBox == '0' ? false : true"
      :check-strictly="chooseOneNode == '0' ? true : false"
      :expand-on-click-node="false"
      :node-key="nodeKey"
      :render-after-expand="false"
      @check="check"
      @node-click="nodeClick"
      @check-change="checkChange"
    >
      <span
        class="el-tree-node__label"
        :class="[data[defaultProps.key]]"
        :nodeKey="data[nodeKey]"
        slot-scope="{ node, data }"
      >
        {{ data._name }}
      </span>
    </el-tree>
  </div>
</template>

<script>
export default dsf.component({
  name: 'DsfTree',
  ctrlCaption: '树',
  mixins: [$mixins.control],
  props: {
    tabIndex: {
      type: [Number, String],
      default: 0,
      desc: '区分是哪一个tab',
    },
    showCheckBox: {
      type: String,
      default: '0',
      desc: '是否启用复选框 1启用/0不启用',
    },
    types: {
      type: String,
      default: '',
      desc: '选择条件',
    },
    chooseOneNode: {
      type: String,
      default: '1',
      desc: "父子是否关联 '0'不关联/'1'",
    },
    defaultOpenSeries: {
      type: String,
      default: '1',
      desc: '展开层级',
    },
    metaDataType: {
      type: String,
      default: '0',
      desc: "数据类型 '0'接口/1元数据组/2本部门/3本单位/4组织架构",
    },
    chooseMetaData: {
      type: Object,
      default() {
        return {}
      },
      desc: '数据类型 选择元数据组后的数据',
    },
    sqlUrl: {
      type: String,
      default: '',
      desc: '数据类型 选择除元数据组后的实际接口地址',
    },
    async: {
      type: String,
      default: '0',
      desc: "是否启用异步加载 '0'不启用/'1'",
    },
    showType: {
      type: String,
      default: 'dept',
      desc: '人员部门选择的 显示类型 user、dept',
    },
    showSubDept: {
      type: String,
      default: '0',
      desc: '组织架构是否异步加载 1:异步加载、2：非异步加载',
    },
    showLevel: {
      type: String,
      default: '0',
      desc: '组织架构显示层级 (0为子机构，不填则显示全部)',
    },
    isDesign: {
      type: Boolean,
      default: true,
    },
    nodeKey: {
      type: String,
      default: '_id',
    }
  },
  data() {
    return {
      filterText: '', //查询关键词（多个关键词空格隔开）
      defaultProps: {
        children: 'children',
        label: '_name',
        key: '_id',
        disabled: this.setNodeDisable,
      },
      data: [],
      loading: false,
      resolve: null,
      sqlUrls: [
        { user: 'oua/getUserByCurDept', dept: 'oua/getUserByCurDept' }, // 本部门
        { user: 'oua/getUserByCurUnit', dept: 'oua/getDeptByCurUnit' }, // 本单位
        { user: 'oua/getUserByOrg', dept: 'oua/getDeptByOrg' }, //组织架构
      ],
      _sqlUrl: this.sqlUrls,
      _async: false,
      params: {},
      asyncSql: '',
      expandedKeys: [],
      idsHasNodeKeysObj: {},
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val)
    },
  },
  created() {
    this._async = this.async === '1'
    this.setRequestConfig()
  },
  methods: {
    reloadData() {
      this.setRequestConfig()
    },
    setRequestConfig() {
      let index = this.metaDataType * 1
      this.params = {}
      if (index > 1) {
        this._sqlUrl = this.sqlUrls[index - 2][this.showType]
        this._async = false
      }
      if (index == 4) {
        this.asyncSql = 'oua/getUserByOrg'

        if (this.showSubDept == '1' && this.showType == 'user') {
          this._async = true
          this.asyncSql = 'oua/getUserByDept'
          this.params = {
            sExplanLevel: this.showLevel,
          }
        } else if (this.showType == 'dept') {
          this.params = {
            sExplanLevel: this.showLevel,
          }
        }
      } else if (index == 0) {
        this._sqlUrl = this.$replace(this.sqlUrl, this)
      } else if (index == 1) {
        let chooseMetaData = this.chooseMetaData || {}
        if (chooseMetaData.type === 'meta') {
          // 元数据
          this._sqlUrl = 'meta/tree/byName'
          this.params = {
            name: chooseMetaData.code.id,
            namespace: this.$vm ? this.$vm.nameSpace : '', //dsfa.rm
            pageName: this.$vm ? this.$vm.pageName : '', //main
            order: '[]',
          }
        } else if (chooseMetaData.type === 'ds') {
          // 数据源
          this._sqlUrl = 'meta/tree/dbSource'
          this.params = {
            rmId: chooseMetaData.code,
          }
        }
      }
      this.loadData()
    },
    loadData() {
      if (this.isDesign) {
        let key = this.defaultProps.key
        let label = this.defaultProps.label
        let children = this.defaultProps.children
        let nodeKey = this.nodeKey
        let data = [
          {
            [key]: 1,
            [label]: '一级 1',
            [nodeKey]: 1,
            [children]: [
              {
                [key]: 4,
                [label]: '二级 1-1',
                [nodeKey]: 4,
                [children]: [
                  {
                    [key]: 9,
                    [label]: '三级 1-1-1',
                    icon: 'el-icon-info',
                    [nodeKey]: 9,
                  },
                  {
                    [key]: 10,
                    [label]: '三级 1-1-2',
                    icon: 'el-icon-info',
                    [nodeKey]: 10,
                  },
                ],
              },
            ],
          },
          {
            [key]: 2,
            [label]: '一级 2',
            [nodeKey]: 2,
            [children]: [
              {
                [key]: 5,
                [label]: '二级 2-1',
                [nodeKey]: 5,
              },
              {
                [key]: 6,
                [label]: '二级 2-2',
                [nodeKey]: 6,
              },
            ],
          },
          {
            [key]: 3,
            [label]: '一级 3',
            [nodeKey]: 3,
            [children]: [
              {
                [key]: 7,
                [label]: '二级 3-1',
                [nodeKey]: 7,
              },
              {
                [key]: 8,
                [label]: '二级 3-2',
                [nodeKey]: 8,
              },
            ],
          },
        ]
        if (this.openWebroot && !!this.webrootName) {
          data = [
            {
              [key]: 0,
              [label]: this.webrootName,
              [children]: data,
              [nodeKey]: 0,
            },
          ]
        }
        this.data = data
        for (let i = 0; i < this.data.length; i++) {
          this.initExpand(this.data[i])
        }
        this.$nextTick(() => {
          this.$dispatchTo('DsfTreeSelector', 'setTreeObj', this)
          this.$dispatchTo('DsfCheckNode', 'setTreeObj', this)
        })
      } else if (!this._async) {
        this.initLoadData()
      }
    },
    initExpand(node, initLevel) {
      // 初始化展开层级处理
      let key = this.nodeKey
      let children = this.defaultProps.children
      if (
        this._async
          ? this.defaultOpenSeries > 1
          : this.defaultOpenSeries != '' && this.defaultOpenSeries >= 0
      ) {
        if (initLevel * 1 < this.defaultOpenSeries * 1) {
          this.expandedKeys.push(node[key])
        }
      } else {
        this.expandedKeys.push(node[key])
      }
      if (node[children] && node[children].length > 0) {
        initLevel = initLevel * 1 + 1
        for (let i = 0; i < node[children].length; i++) {
          this.initExpand(node[children][i], initLevel)
        }
      }
    },
    setNodeDisable(data, node) {
      let _id = data[this.defaultProps.key]
      if (!this.idsHasNodeKeysObj[_id]) {
        this.idsHasNodeKeysObj[_id] = []
      }
      let globalId = data[this.nodeKey]
      if (!this.idsHasNodeKeysObj[_id].includes(globalId)) {
        this.idsHasNodeKeysObj[_id].push(globalId)
      }
      data['_isDisabled'] = false
      if (this.types && !this.$eval(this.types, data)) {
        data['_isDisabled'] = true
        return true
      } else {
        return false
      }
    },
    initLoadData(resolve) {
      const self = this
      self.loading = true
      dsf.mix(this.params, this.queryString)

      let loader = dsf.layer.loading()
      self.$http
        .get(self._sqlUrl, this.params)
        .done((res) => {
          if (res.success) {
            let list = res.data,
              temp1 = {},
              tree = [],
              initLevel = null
            if (!Array.isArray(list)) {
              list = [list]
            }
            for (let i = 0; i < list.length; i++) {
              let node = list[i]
              temp1[node[self.defaultProps.key]] = node
              if (temp1[node.treeinfo_pid]) {
                if (!temp1[node.treeinfo_pid]['children']) {
                  temp1[node.treeinfo_pid]['children'] = []
                }
                temp1[node.treeinfo_pid]['children'].push(node)
              } else {
                tree.push(node)
              }

              // 展开层级
              if (i == 0) {
                self.expandedKeys = []
              }
              self.$nextTick(()=>{
                self.initExpand(node, node.treeinfo_level || 0)
              })
            }

            self.data = tree
            self._async = false
            self.$dispatch('load-data', tree)
            if (resolve) {
              return resolve(tree)
            }
          } else {
            dsf.layer.message(res.message, false)
          }
        })
        .error((res) => {
          dsf.layer.message(res.error.message, false)
        })
        .always(() => {
          self.loading = false
          self.$refs?.tree?.filter(self.filterText)
          self.$dispatchTo('DsfTreeSelector', 'setTreeObj', self)
          self.$dispatchTo('DsfCheckNode', 'setTreeObj', self)
          dsf.layer.closeLoading(loader)
        })
    },
    asyncLoadData(node, resolve) {
      const self = this
      let params = { deptID: node.data[self.defaultProps.key] }
      dsf.mix(params, this.queryString)
      self.$http
        .get(self.asyncSql, params)
        .done((res) => {
          if (res.success) {
            let list = res.data,
              temp1 = {},
              tree = []
            if (!Array.isArray(list)) {
              list = list.children
            }
            for (let i = 0; i < list.length; i++) {
              let node = list[i]
              temp1[node[self.defaultProps.key]] = node
              if (temp1[node.treeinfo_pid]) {
                if (!temp1[node.treeinfo_pid]['children']) {
                  temp1[node.treeinfo_pid]['children'] = []
                }
                temp1[node.treeinfo_pid]['children'].push(node)
              } else {
                tree.push(node)
              }
            }
            self.$dispatch('load-data', tree)
            if (resolve) {
              return resolve(tree)
            }
          } else {
            dsf.layer.message(res.message, false)
          }
        })
        .error((res) => {
          dsf.layer.message(res.error.message, false)
        })
    },
    lazyLoad(node, resolve) {
      this.resolve = resolve
      if (node.level === 0) {
        this.initLoadData(resolve)
      } else if (node.data.children) {
        this.asyncLoadData(node, resolve)
      } else {
        resolve([])
      }
    },
    filterNode(value, data, node) {
      // if (!value) return true;
      // return data[this.defaultProps.label].indexOf(value) !== -1;
      let keywords = value.trim()
      if (keywords === '') {
        keywords = []
      } else {
        keywords = keywords.split(' ')
      }

      if (!keywords.length) {
        return true
      }

      // if (!node.parent) {
      //   node.filterText = null
      // } else if (node.parent.visible && node.parent.filterText === value) {
      //   node.filterText = value
      //   return true
      // }
      // for (let i = 0; i < keywords.length; i++) {
      //   if (data[this.defaultProps.label].indexOf(keywords[i]) === -1) {
      //     node.filterText = value
      //     return false
      //   }
      // }
      // node.filterText = value
      // return true
      
      for (let i = 0; i < keywords.length; i++) {
        if (data[this.defaultProps.label]?.indexOf(keywords[i])> -1) {
          return true;
        }
      }
      return false;
    },
    checkChange(node, state, child) {
      if (!state) {
        this.$dispatchTo('DsfTreeSelector', 'deleteSelectValue', node)
        this.$dispatchTo('DsfCheckNode', 'deleteSelectValue', node)
      }
      // 同_id值处理
      let _id = node[this.defaultProps.key]
      let nodes = this.idsHasNodeKeysObj[_id]
      if (nodes.length > 1) {
        _.forEach(nodes, (node) => {
          let otherNode = this.$refs.tree.getNode(node)
          if (otherNode.checked != state) {
            this.$refs.tree.setChecked(node, state, false)
          }
        })
      }
    },
    check(node, data) {
      if (!node?.[this.defaultProps.key]) return
      const self = this
      let datas = data.checkedNodes
      let deleteDatas = []
      // 父子关联带父级
      if (self.chooseOneNode == '1' && (data?.halfCheckedNodes || []).length) {
        datas.unshift(...data.halfCheckedNodes)
        datas.shift()
      }
      _.forEachRight(datas, (item, i) => {
        item.tabIndex = self.tabIndex
        if (self.types && !this.$eval(self.types, item)) {
          self.$refs.tree.setChecked(item[self.nodeKey], false, false)
          deleteDatas.push(item)
          datas.splice(i, 1)
        }
        // 父子关联不带父级
        else if (
          this.chooseOneNode == '2' &&
          (item.isLeaf === false || !!item.children?.length)
        ) {
          datas.splice(i, 1)
        }
      })
      this.$dispatchTo('DsfTreeSelector', 'setSelectedData', {
        data: datas,
        deleteData: deleteDatas,
        tabIndex: this.tabIndex,
      })
      this.$dispatchTo('DsfCheckNode', 'setSelectedData', {
        data: datas,
        deleteData: deleteDatas,
        tabIndex: this.tabIndex,
      })
    },
    nodeClick(node, data) {
      // 点击禁止项的子父级关联处理1
      if (this.chooseOneNode * 1 && node._isDisabled) {
        node._checked = !node._checked
        if (this.$refs.tree.getCheckedNodes().length == 0) {
          node._checked = true
        }
        // 如果在子父级关联的情况下,且选择的又是不能被选择的，那它需要带上父子级别
        this.$refs.tree.setChecked(data[this.nodeKey], node._checked, true)
        this.check({}, { checkedNodes: this.$refs.tree.getCheckedNodes(true) })
        this.nodeClickAfter(node, node._checked)
      }
    },
    nodeClickAfter(node, _checked) {
      // 点击禁止项的子父级关联处理2
      if (node.children && node.children.length > 0) {
        for (let i in node.children) {
          node.children[i]._checked = _checked
          this.nodeClickAfter(node.children[i], _checked)
        }
      }
    },
  },
  design: {
    //设计器是是否在组件上产生遮罩
    isMask: false,
    fit: true,
  },
})
</script>
