<template>
  <div class="ds-control ds-form-item ds-area-select" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">{{ getShowContent() }}</template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :class="{'ds-error-border':errors.length>0}" :style="getFormItemBlockMarginLeft()">
        <!--只读状态只显示div-->
        <div v-if="readOnly" class="ds-form-readonly">
          <span>{{ getShowContent() }}</span>
        </div>
        <!--非只读状态：内联-->
        <div v-else-if="displayMode === 1" class="ds-area-select-wrap">
          <template v-for="i in (rangeNum = range[1] - range[0] + 1)">
            <dsf-area-select-item
              :key="i"
              ref="item"
              :is-design="isDesign"
              :code="range[0] - 1 + i"
              :is-first="i == 1"
              :is-last="i == rangeNum"
            />
          </template>
        </div>
        <!--非只读状态：级联-->
        <template v-else-if="displayMode === 2">
          <dsf-area-cascader
            :is-design="isDesign"
            :range-start="+range[0]"
            :range-end="+range[1]"
          />
        </template>
        <slot name="error">
          <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        </slot>
      </div>
    </template>
  </div>
</template>

<script>
import DsfAreaSelectItem from './areaSelectItem';
import DsfAreaCascader from './areaCascader';
import { getValuesByLastValue } from './utils/areaUtils';

export default dsf.component({
  name: "DsfAreaSelect",
  components: {
    DsfAreaSelectItem,
    DsfAreaCascader
  },
  mixins: [$mixins.formControl],
  ctrlCaption: "行政区划",
  design: {
    layout: {
      exclude: ["value", "items"],
    }
  },
  props: {
    placeholder: {
      type: String,
      default: "请选择",
    },
    value: {
      type: Object,
      default() {
        return {};
      },
    },
    superCode: {
      type: [String, Number],
      default: "000000000000",
    },
    fixedCode: {
      type: String,
      default: "",
    },
    range: {
      type: Array,
      default() {
        return ["0", "4"];
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
    //展示方式 1内联 | 2级联
    displayMode: {
      type: Number,
      default: 1
    },
    // 是否自动选择下一级第一个
    autoSelect: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      data: [],
      valueCopy: null
    }
  },
  computed: {
    // 对value值得value逆向解析出所有的key
    // 用于数据复现
    ids() {
      if (!this.value?.value) {
        return [];
      }
      return getValuesByLastValue(this.value.value);
    }
  },
  watch: {
    'value.value': {
      handler(to) {
        // 如果从外部改变的value
        if (to !== this.valueCopy?.value) {
          // todo
        }
      },
      immediate: true
    }
  },
  methods: {
    // 清空value值
    reset() {
      this.valueCopy = {value: '', text: ''};
      this.emitValueChange(this.valueCopy);
    },
    // 更新value值
    updateValue() {
      let items = this.$refs.item;
      if (items.length) {
        let texts = [], vals = [];
        items.forEach(({value}) => {
          if (value) {
            texts.push(value.name);
            vals.push(value.areacode);
          }
        });
        this.valueCopy = {
          value: vals.pop(),
          text: texts.join('-')
        };
        this.emitValueChange(this.valueCopy);
        this.$nextTick(() => {
          this.emitFormValidate(this.valueCopy);
        })
      }
    }
  }
});
</script>