<template>
  <div class="ds-control ds-navtree ds-tree ds-no-padding">
    <DsfPanel>
      <template v-slot:title>
        {{ label }}
      </template>
      <template>
        <el-input
          v-if="openSearchBox"
          v-model="filterText"
          style="margin-bottom: 15px"
          placeholder="查询（多个关键词空格隔开）"
        />
        <DsfVirtualScroll class="ds-tree-box" scroll-y scroll-x>
          <el-tree
            ref="tree"
            class="filter-tree ds-tree-select-tree-box"
            :empty-text="loading ? '数据加载中...' : '暂无数据'"
            :props="defaultProps"
            :default-expanded-keys="expandedKeys"
            :lazy="async && !isDesign && !initDataSource !== '3'"
            :load="lazyLoad"
            :data="data"
            :highlight-current="true"
            :expand-on-click-node="false"
            :filter-node-method="filterNode"
            node-key="_id"
            @current-change="currNodeChange"
            @node-expand="nodeExpand"
            @node-collapse="nodeCollapse"
          >
            <div slot-scope="{ node, data }" class="custom-tree-node">
              <template v-if="!icons">
                <i
                  v-if="data[defaultProps.children] !== undefined && showIcon"
                  class="iconfont"
                  :class="[node.expanded ? 'icon-dakai' : 'icon-wenjianjia']"
                />
                <i v-else-if="showIcon" class="iconfont icon-shiyongwendang" />
                <span v-html="data[defaultProps.label]"></span>
              </template>
              <template v-else>
                <i
                  v-for="(icon, iconI) in icons.filter((icon) => {
                    return getIconClass(icon, node)[0] != '';
                  })"
                  :key="iconI"
                  class="iconfont"
                  :class="getIconClass(icon, node)"
                  :style="{ color: icon.color }"
                ></i>
                <span v-html="data[defaultProps.label]"></span>
              </template>
            </div>
          </el-tree>
        </DsfVirtualScroll>
      </template>
    </DsfPanel>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfNavTree",
  ctrlCaption: "导航树",
  mixins: [$mixins.control, $mixins.tree],
  design: {
    //组件加载到设计器上时组件高度默认在父容器铺满
    fit: true,
    metadata: {
      isDeep: false,
      create(root) {
        let query = {};
        let searchBox = [],
          attachSearch = [];
        for (let i = 0; i < this.searchInfo.length; i++) {
          let s = this.searchInfo[i];
          let item = null;
          if (s.type == "0") {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: "searchValue",
            };
            searchBox.push(item);
          } else {
            item = {
              code: s.code,
              operator: s.operation,
              valueKey: s.code,
            };
            attachSearch.push(item);
            s.metadata_fullcode = item.code;
          }
        }
        //添加将检索框条件
        let seachBoxitem = { logic: "or", items: [] };
        seachBoxitem.items.push(...searchBox);

        //添加默认附加条件
        let attachSearchItem = { logic: "and", items: [] };
        attachSearchItem.items.push(...attachSearch);

        let searchitems = { logic: "and", items: [] };
        searchitems.items.push(seachBoxitem);
        searchitems.items.push(attachSearchItem);
        // seachBoxitem.items.length > 0 && searchitems.items.push(seachBoxitem);
        // attachSearchItem.items.length > 0 && searchitems.items.push(attachSearchItem);
        query = searchitems;
        let metadata = {
          query: query,
          self: this,
          metadata: this.metadata,
          browseScope: this.browseScope,
        };
        return [metadata];
      },
    },
  },
  props: {
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("nav-tree-meta-data");
      },
      desc: "元数据",
    },
    label: {
      type: String,
      default: "导航树",
      desc: "标题",
    },
    showIcon: {
      type: Boolean,
      default: true,
      desc: "显示节点图标",
    },
    openSearchBox: {
      type: Boolean,
      default: true,
      desc: "启用搜索框",
    },
    openWebroot: {
      type: Boolean,
      default: false,
      desc: "启用虚拟根目录",
    },
    webrootName: {
      type: String,
      default: "",
      desc: "虚拟根目录的节点名称",
    },
    targetIframeName: {
      type: String,
      default: "",
      desc: "跳转iframe的NAME标识",
    },
    targetIframeSrc: {
      type: String,
      default: "",
      desc: "跳转iframe地址",
    },
    pId: {
      type: String,
      default: "",
      desc: "根节点数据",
    },
    globalId: {
      type: String,
      default: "",
      desc: "全局ID",
    },
    maxLevel: {
      type: String,
      default: "",
      desc: "最大层级",
    },
    defaultOpenSeries: {
      type: String,
      default: "1",
      desc: "初始化展开至第几级",
    },
    defaultOrder: {
      type: String,
      default: "",
      desc: "默认排序代码: xx desc,",
    },
    searchInfo: {
      type: Array,
      default() {
        return [];
      },
      desc: '查询条件,"dsfa_rm.type_value": "0"',
    },
    initDataSource: {
      type: String,
      default: "1",
      desc:
        "初始化数据来源，1:代表页面元数据，2:代表可选择数据，3:代表自定义接口",
    },
    initDataSourceUrl: {
      type: String,
      default: "",
      desc: "自定义接口地址",
    },
    chooseMetaData: {
      type: Object,
      default() {
        return {
          attach:{}
        };
      },
      desc: "初始化选择的数据来源及选择时的具体数据",
    },
    async: {
      type: Boolean,
      default: false,
      desc:
        "是否启用异步,在初始化数据来源为自定义接口的时候，就算启用也不会生效",
    },
    asyncDataSource: {
      type: String,
      default: "1",
      desc: "初始化数据来源，1:代表页面元数据，2:代表可选择数据",
    },
    asyncChooseMetaData: {
      type: Object,
      default() {
        return {};
      },
      desc: "异步请求选择的数据来源及选择时的具体数据",
    },
    metadata_fullcode: {
      type: String,
      default: "",
    },
    browseScope: {
      type: Object,
      default() {
        return {
          scope: "2",
          unit: [],
        };
      },
      desc: "发送范围",
    },
    icons: {
      type: Array,
      default() {
        return [
          {
            code: "!@[children]",
            iconExpand: "icon-shiyongwendang",
            iconFold: "icon-shiyongwendang",
            color: "#6594dc",
          },
          {
            code: "!!@[children]",
            iconExpand: "icon-dakai",
            iconFold: "icon-wenjianjia",
            color: "#c78511",
          },
        ];
      },
      desc: "图标设置",
    }
  },
  data() {
    return {
      expandedKeys: [],
      filterText: "",
      defaultProps: {
        children: "children",
        label: "_name",
        key: "_id",
      },
      data: [],
      emptyText: "数据加载中...",
      resolve: null,
      metadataFullcode: this.metadata_fullcode,
      loading: false,
      currNode: null,
    };
  },
  computed: {
    keywords() {
      const keyword = this.filterText.trim();
      if (keyword === "") {
        return [];
      }
      return keyword.split(" ");
    },
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
  },
  created() {
    if (this.$vm && this.$vm.isPreView) {
      if (!this.$vm.nameSpace) {
        this.$vm.nameSpace = ""; //"dsfa.rm";
      }
      if (!this.$vm.pageName) {
        this.$vm.pageName = ""; //"main";
      }
      this.metadataFullcode = ""; //"dsfa_rm.NavTree4";
    }
    this.loadData(true, false);
  },
  methods: {
    nodeExpand(data) {
      let key = this.defaultProps.key;
      dsf.array.ensure(this.expandedKeys, data[key]);
    },
    nodeCollapse(data, node) {
      let key = this.defaultProps.key;
      dsf.array.remove(this.expandedKeys, data[key]);
      _.each(node.childNodes, (it) => {
        this.nodeCollapse(it.data, it);
      });
    },
    loadData(isInit, isReload) {
      if (this.isDesign) {
        let root = loadDesignerDefaultData.call(this);
        let key = this.defaultProps.key;
        let label = this.defaultProps.label;
        let children = this.defaultProps.children;
        if (this.openWebroot && !!this.webrootName) {
          root = [
            {
              [key]: 0,
              [label]: this.webrootName,
              [children]: root,
            },
          ];
        }
        this.data = root;
        _.each(this.data, (it) => {
          this.initExpand(it);
        });
      } else if (this.$isPrototype && this.initDataSource == '4') {
        this.loadDataSuccessAfterFn(false,this.chooseMetaData.attach || [],isReload);
      } else {
        if (isInit) {
          this.initLoadData(null, false);
        } else if (isReload) {
          this.initLoadData(null, true);
        }
      }
    },
    initExpand(node, initLevel) {
      // 初始化展开层级处理
      let key = this.defaultProps.key;
      let children = this.defaultProps.children;
      let nodeLevel =
        (node["_level"] == undefined
          ? node["treeinfo_level"]
          : node["_level"]) * 1;
      if (initLevel !== undefined && this.defaultOpenSeries > 0) {
        if (nodeLevel < (initLevel * 1 + this.defaultOpenSeries * 1 - 1)) {
          dsf.array.ensure(this.expandedKeys, node[key]);
        }
      } else {
        dsf.array.ensure(this.expandedKeys, node[key]);
      }
      if (node[children] && node[children].length > 0) {
        for (let i = 0; i < node[children].length; i++) {
          this.initExpand(node[children][i], initLevel);
        }
      }
    },
    initLoadData(resolve, isReload) {
      const self = this;
      self.loading = true;
      let sourceType = this.initDataSource * 1;
      let sourceConfig = this.chooseMetaData;
      let url = "meta/tree";
      let args = {
        pid: self.pId,
        metaFullCode: self.metadataFullcode,
        namespace: self.$vm.nameSpace,
        pageName: self.$vm.pageName,
        globalIdValue: self.globalId,
        query: null,
        maxLevel: self.maxLevel,
        order: [],
      };

      if (self.async && self.initDataSource !== "3") {
        args.openLevel = (!this.defaultOpenSeries || this.defaultOpenSeries <= 1) ? 1 : this.defaultOpenSeries;
      }
      if (sourceType === 2 && sourceConfig.type) {
        if (sourceConfig.type == "meta") {
          // 元数据获取树
          args.name = sourceConfig.code.id;
          url = "meta/tree/byName";
        } else if(sourceConfig.type == "static"){
          self.loading = false;
          return false;
        } else if(sourceConfig.type == "ds"){
          // 数据源获取树
          args = {
            rmId: sourceConfig.code,
          };
          url = "meta/tree/dbSource";
        }
      } else if (sourceType === 3) {
        url = self.initDataSourceUrl;
        args = {};
      } 

      // 整理查询条件
      let queryData = {
        searchValue: "",
      };
      let searchInfo = self.searchInfo;
      for (let i = 0; i < searchInfo.length; i++) {
        let item = searchInfo[i];
        if (item.type == "1") {
          let arr = item.code.split(".");
          if (arr.length == 2) {
            queryData[item.code] = item.valueExpress;
          } else {
            queryData[item.metadata_fullcode] = item.valueExpress;
          }
        }
      }

      // 整理排序代码
      let defaultOrder = $.trim(self.defaultOrder).split(",");
      for (let i = 0; i < defaultOrder.length; i++) {
        let o = defaultOrder[i];
        let reg = /(.+)\s+(desc|asc)/gim;
        let s = $.trim(o);
        if (s) {
          let result = reg.exec(s);
          if (result) {
            let obj = {
              code: result[1], //list.root.scope + "." + result[1],
              sort: result[2] || "ASC",
            };
            args.order.push(obj);
          }
        }
      }
      args.order = JSON.stringify(args.order);
      args.query = JSON.stringify(queryData);

      // url += dsf.url.stringify(self.$route.query);
      dsf.mix(args, this.queryString);
      self.$http
        .get(url, args, sourceType !== 3)
        .done((res) => {
          if (res.success) {
            this.loadDataSuccessAfterFn(resolve,res.data);
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error((res) => {
          dsf.layer.message(
            res.message || res.error?.message || "程序异常",
            false
          );
        })
        .always(() => {
          self.loading = false;
        });
    },
    loadDataSuccessAfterFn(resolve,list,isReload) {
      let temp1 = {},
        tree = [],
        initLevel = 0,
        choosedNode = {},
        self = this;
      if (!Array.isArray(list)) {
        list = [list];
      }
      for (let i = 0; i < list.length; i++) {
        let node = list[i];
        temp1[node[self.defaultProps.key]] = node;
        if (temp1[node.treeinfo_pid]) {
          if (!temp1[node.treeinfo_pid]["children"]) {
            temp1[node.treeinfo_pid]["children"] = [];
          }
          temp1[node.treeinfo_pid]["children"].push(node);
        } else {
          tree.push(node);
        }

        // 展开层级
        if (i == 0) {
          self.expandedKeys = [];
          initLevel = node["treeinfo_level"];
          choosedNode = node;
        }
        if (self.defaultOpenSeries != 1) {
          self.initExpand(node, initLevel);
        }
      }

      if (self.openWebroot && !!self.webrootName) {
        choosedNode = {
          [self.defaultProps.key]: "虚拟根目录",
          [self.defaultProps.label]: self.webrootName,
          [self.defaultProps.children]: tree
        };
        tree = [choosedNode];
        self.expandedKeys.push("虚拟根目录");
      }
      self.data = tree;
      self.$nextTick(() => {
        // 默认选中第一个节点
        // self.loadIframe(choosedNode);
        if (!isReload) {
          self.$refs.tree?.setCurrentKey(choosedNode[self.defaultProps.key]);
          self.loadIframe(choosedNode);
        } else {
          self.$refs.tree?.setCurrentKey(this.currNode._id);
        }
      });
      if (resolve) {
        return resolve(tree);
      }
    },
    asyncLoadData(node, resolve, isReload) {
      const self = this;
      let args = {
        metaFullCode: self.metadataFullcode,
        namespace: self.$vm.nameSpace,
        pageName: self.$vm.pageName,
        maxLevel: self.maxLevel,
        pid: node.data[self.defaultProps.key],
      };
      dsf.mix(args, this.queryString);
      self.$http
        .get("meta/tree/child", args)
        .done((res) => {
          if (res.success) {
            let list = res.data,
              temp1 = {},
              tree = [];
            for (let i = 0; i < list.length; i++) {
              let node = list[i];
              temp1[node[self.defaultProps.key]] = node;
              if (temp1[node.treeinfo_pid]) {
                if (!temp1[node.treeinfo_pid]["children"]) {
                  temp1[node.treeinfo_pid]["children"] = [];
                }
                temp1[node.treeinfo_pid]["children"].push(node);
              } else {
                tree.push(node);
              }
            }
            if (resolve) {
              return resolve(tree);
            }
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error((res) => {
          dsf.layer.message(res.error.message, false);
        });
    },
    lazyLoad(node, resolve, isReload) {
      this.resolve = resolve;
      if (node.level === 0) {
        this.initLoadData(resolve, isReload);
      } else {
        this.asyncLoadData(node, resolve, isReload);
      }
    },
    filterNode(value, data, node) {
      // if (!this.keywords.length) {
      //   return true;
      // }
      // if (!node.parent) {
      //   node.filterText = null;
      // } else if (node.parent.visible && node.parent.filterText === value) {
      //   node.filterText = value;
      //   return true;
      // }
      // for (let i = 0; i < this.keywords.length; i++) {
      //   if (data[this.defaultProps.label]?.indexOf(this.keywords[i]) === -1) {
      //     node.filterText = value;
      //     return false;
      //   }
      // }
      // node.filterText = value;
      // return true;
      if (!value) return true;
      for (let i = 0; i < this.keywords.length; i++) {
        if (data[this.defaultProps.label]?.indexOf(this.keywords[i])> -1) {
          return true;
        }
      }
      return false;
    },
    currNodeChange(data, node) {
      this.currNode = data;
      this.loadIframe(data, node);
    },
    loadIframe(data, node) {
      if (this.$vm && this.$vm.frames) {
        let view = this.$vm.frames[this.targetIframeName];
        if (view) {
          let url = this.$replace(this.targetIframeSrc, data);
          view.$emit("update:path", url);
        }
      }
    },
    reloadData() {
      this.data = [];
      if (this.resolve) {
        this.resolve([]);
        this.lazyLoad({ level: 0 }, this.resolve, true);
      } else {
        this.loadData(false, true);
      }
    },
    getIconClass(iconData, nodeData) {
      if (dsf.express.eval(iconData.code, nodeData.data)) {
        if (nodeData.expanded) {
          return [iconData.iconExpand || ""];
        } else {
          return [iconData.iconFold || ""];
        }
      }
      return [""];
    }
  },
});
function loadDesignerDefaultData() {
  let designerData = [
    {
      [this.defaultProps.key]: 1,
      [this.defaultProps.label]: "一级",
      [this.defaultProps.children]: [
        {
          [this.defaultProps.key]: 2,
          [this.defaultProps.label]: "二级 1-1",
          icon: "el-icon-info",
        },
        {
          [this.defaultProps.key]: 3,
          [this.defaultProps.label]: "二级 1-2",
          icon: "el-icon-info",
        },
      ],
    },
  ];
  return designerData;
}
</script>
