<template>
  <div v-show="loaded" class="ds-super-search" :class="{ 'row-split-boder': superSearchRowBorder }">
    <div class="ds-super-search-row el-row" v-if="showFitler">
      <div class="ds-super-search-item el-col-24 filter-bar">
        <label class="ds-super-search-item-text">筛选:</label>
        <div class="ds-super-search-item-block">
          <DsfSuperSearchFilter v-for="item in getFilterBar()" :key="item.id" class="ds-super-search-filter-item" :item="item" @change="valueChange($event, item)" @remove="removeFilter($event)"></DsfSuperSearchFilter>
        </div>
      </div>
    </div>
    <div class="ds-super-search-main" :mode="buttonBarPositionMode" :class="{'ds-super-search-main-extend':isExtend}">
      <div class="ds-super-search-main-left">
        <template v-for="(cells, $index) in layout">
          <div
            :key="$index"
            v-if="($index+1)<=defaultRowsNumber||isExtend"
            class="ds-super-search-row el-row"
            :col="6"
            :class="{
              'has-buttons':buttonBarPositionMode == 'normal' && $index == 0 &&firstHasButton,
              'ismore':($index+1)>defaultRowsNumber
            }">
            <DsfSuperSearchItem
              v-for="item in cells"
              :key="item.id"
              :item-key="item.id"
              :item="item"
              :class="[
                'el-col-' + item.layout,
                item.showModel == 'tile' ? 'tile' : '',
              ]"
              @focus="filterAllClose"
              @change="valueChange($event, item)">
            </DsfSuperSearchItem>
          </div>
        </template>
      </div>
      <div
        v-if="firstHasButton"
        class="first-row-button-row"
        :style="{
          'height': buttonBarPositionMode == 'normal' || isExtend ? 'auto': `${defaultRowsNumber * 46}px`
        }"
        :show="isExtend"
        :mode="buttonBarPositionMode">
        <slot name="firstRowButtonBar"></slot>
      </div>
    </div>
    <div v-if="hasExtend" class="ds-super-search-extend">
      <span @click="extendHandler">
        {{ !isExtend ? "更多筛选条件" : "收起筛选条件" }}
        <i class="closearrow iconfont ds-font-6" :class="{ 'icon-xia1': !isExtend, 'icon-shang1': isExtend }" style="margin-left: 10px"></i>
      </span>
    </div>
  </div>
</template>
<script>
import DsfSuperSearchFilter from "./superSearchFilter";
import DsfSuperSearchItem from "./superSearchItem";
export default {
  name: "DsfSuperSearch",
  inheritAttrs: false,
  components: {
    DsfSuperSearchFilter,
    DsfSuperSearchItem
  },
  provide() {
    return {
      $superSearch: this
    };
  },
  props: {
    buttonBarPositionMode: {
      // normal  column
      type: String,
      default: () => "normal"
    },
    owner: {
      type: Object,
      default() {
        return null;
      }
    },
    firstHasButton: {
      type: Boolean,
      default: false
    },
    searchItemSetting: {
      type: Array,
      default() {
        return [];
      }
    },
    searchHeight: {
      type: Number,
      default: 0
    },
    defaultRowsNumber: {
      type: Number,
      default: 2
    },
    showFitler: {
      type: Boolean,
      default: false
    },
    superSearchRowBorder: {
      type: Boolean,
      default: true
    },
    queryValue: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    // console.log(dsf.config.component.superSearch);
    return {
      isExtend: false,
      searchItems: [],
      optionsMap: {},
      totalRowsNumber: 0,
      layout: [],
      loaded: false,
      isFirstLoad: false,
      rowHeight: 45,
      defaultHeight: 0,
      value: this.queryValue || {},
      config: dsf.mix(true, {}, dsf.config.component.superSearch)
    };
  },
  computed: {
    hasExtend() {
      if (this.totalRowsNumber > this.defaultRowsNumber) {
        return true;
      }
      return false;
    },
    initRowsNumber() {
      let rows = this.totalRowsNumber < this.defaultRowsNumber ? this.totalRowsNumber : this.defaultRowsNumber;
      return rows;
    }
  },
  watch: {
    searchItemSetting: {
      async handler(v) {
        await this.updateSearchItemSetting();
        this.emitHeight();
      },
      immediate: true
    },
    showFitler: {
      handler(v) {
        this.$nextTick(() => {
          this.emitHeight();
        });
      }
    },
    defaultRowsNumber: {
      handler(v) {
        this.$nextTick(() => {
          this.emitHeight();
        });
      }
    }
  },
  created() {
    window.addEventListener("resize", this.resize);
    document.addEventListener("click", this.documentClick);
    this.totalRowsNumber = this.getTotalRowsNumber();
  },
  mounted() {
    // this.optionsMap = await this.updateData();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resize);
    document.removeEventListener("click", this.documentClick);
  },
  methods: {
    documentClick() {
      this.filterAllClose();
    },
    getFilterBar() {
      let filter = [];
      // let value = this.value;
      _.each(this.searchItems, (el) => {
        let val = el.values ? el.values : null; //value ? value[el.field] : null;
        if (dsf.isDef(val)) {
          let v = dsf.type(val) == "array" ? val : [val];
          v = _.filter(v, (el) => el);
          if (v.length > 0) {
            filter.push(el);
          }
        }
      });
      return filter;
    },
    removeFilter(item) {
      if (item.type == "enum") {
        item.values = [];
        item.customerBetween = [];
        item.treeNodeSelected = [];
      } else if (item.type == "datetime") {
        if (item.operator == "between") {
          item.values = [];
        } else {
          item.values = "";
        }
      } else if (item.type == "text") {
        item.values = "";
      }
      this.postSearch(true);
    },
    filterAllClose(exclude) {
      _.each(this.searchItems, (v) => {
        if (v !== exclude) {
          v.isFilterExtend = false;
          // v.multiple = false;
          // v.isExtend = false;
          v.tileMultipleState = false;
        }
      });
    },
    filterClose(item) {
      item.isFilterExtend = false;
      item.multiple = false;
    },

    async updateSearchItemSetting() {
      this.searchItems = _.map(this.searchItemSetting, (el) => {
        // let v = _this.$eval(el.defaultValue, this.owner.$vm);
        this.totalRowsNumber = this.getTotalRowsNumber();
        let defaultValue = null;
        if (dsf.isDef(el.defaultValue) && el.defaultValue != "") {
          defaultValue = this.owner.$eval(el.defaultValue, this.owner.$vm);
        }
        if (el.searchType == "enum") {
          if (dsf.isDef(defaultValue)) {
            defaultValue = dsf.type(defaultValue) != "array" ? [defaultValue] : defaultValue;
          } else {
            defaultValue = [];
          }
          defaultValue = _.map(defaultValue, (v) => v + "");
        } else if (el.searchType == "datetime") {
          if (el.operator == "between") {
            if (dsf.isDef(defaultValue)) {
              defaultValue = dsf.type(defaultValue) != "array" ? [defaultValue] : defaultValue;
            } else {
              defaultValue = [];
            }
          }
        } else {
          defaultValue = dsf.isDef(defaultValue) ? defaultValue + "" : "";
        }
        // let v = this.value ? this.value[el.field] : null;
        return {
          //元数据标识
          id: el.id,
          //字段名称
          field: el.field,
          //字段描述文本
          label: el.fieldCaption,
          //高级检索配置内容，比如数据字典等
          valueSource: el.setting,
          //操作符号
          operator: el.operator,
          //检索类型
          type: el.searchType,
          //绑定数据字典的key
          optionKey: el.id || "",
          //用户选择的值
          options: [],
          //树形结构选中存储
          treeNodeSelected: [],
          //提交给后端的值
          values: defaultValue,
          //显示方式，文本框、日期等
          showModel: el.model,
          //是否点击了更多展开
          isExtend: false,
          //是否有更多按钮
          isMore: false,
          //是否点击多选后展开
          tileMultipleState: false,
          //是否多选状态
          multiple: false,
          //是否允许多选按钮
          isAllowMultipe: el.searchType == "enum" && el.operator == "between" ? false : true,
          //布局
          layout: el.layout || 24,
          //区间选择是自定义输入内容
          customerBetween: [],
          //是否是筛选条展开
          isFilterExtend: false,
          //字段
          fields: _.filter(el.children, (c) => c.join == 1),
          // 是否断行
          isLineBreak: el.isLineBreak || false,
          // 级联子级字段名
          childField: el.childField || null
        };
      });
      this.optionsMap = await this.updateData();
      // console.log(this.optionsMap);
      //将options选项值赋予到每个相关的item中
      // console.log(this.searchItems)
      _.each(this.searchItems, (item, key) => {
        if (this.optionsMap[item.id]) {
          item.options = this.optionsMap[item.id];
        }
      });
      this.setLayout();
      this.loaded = true;
      this.$nextTick(() => {
        if (!this.isFirstLoad) {
          this.isFirstLoad = true;
          this.resize();
          let v = this.emitValue();
          this.$nextTick(() => {
            this.$emit("loaded", v);
          });
        }
      });
    },
    async updateData() {
      if (this.owner.isDesign) {
        return {};
      }
      let optionsMap = {};
      let optionsRequests = [];
      let dictArr = [];
      _.each(this.searchItems, (v) => {
        if (v.type == "enum") {
          if (v.valueSource?.type == "dict") {
            let params = {
              key: v.id,
              dictId: v.valueSource.value.key
            };
            dictArr.push(params);
          } else if (v.valueSource?.type == "dbsource") {
            let path = v.valueSource.value.key.replace(/\./g, "/");
            optionsRequests.push({
              id: v.id,
              request: (params) => {
                let p = {
                  ...(this.owner?.queryString || {}),
                  ...(params || {})
                };
                return this.$webAPI.dbSourceInterface(p, path);
              }
            });
          } else if (v.valueSource?.type == "interface") {
            optionsRequests.push({
              id: v.id,
              request: (params) => {
                let p = {
                  ...(this.owner?.queryString || {}),
                  ...(params || {})
                };
                return this.$http.get(this.$replace(v.valueSource.value), p);
              }
            });
          } else {
            if (v.valueSource && v.valueSource.value) {
              optionsMap[v.id] = v.valueSource.value;
            } else {
              optionsMap[v.id] = [];
            }
          }
        }
      });
      if (dictArr.length > 0) {
        let params = {
          dicts: dictArr
        };
        optionsRequests.unshift({
          id: "dicts",
          request: () => {
            return this.$webAPI.getBatchDict(params);
          }
        });
      }
      try {
        let optionsMap2 = await this.loopOptionsHttps(optionsRequests);
        return dsf.mix(optionsMap, optionsMap2, {});
      } catch (err) {
        // dsf.layer.message("查询检索选项出现错误", false);
        dsf.layer.pc.message("查询检索选项出现错误,页面无法创建", false);
      }
    },
    loopOptionsHttps(requests, callback) {
      let optionsMap = {};
      let p = new Promise((reslove, reject) => {
        let index = -1;
        let next = function () {
          index++;
          if (index >= requests.length) {
            reslove(optionsMap);
            return;
          }
          if (requests.length > 0) {
            exec();
          }
        };
        function exec() {
          let opts = requests[index];
          opts
            .request()
            .then(({ data }) => {
              if (opts.id == "dicts") {
                for (let k in data.data) {
                  optionsMap[k] = data.data[k];
                }
              } else {
                optionsMap[opts.id] = data.data;
              }
              if (callback) {
                callback(
                  {
                    request: requests[index],
                    data: data.data
                  },
                  next
                );
              } else {
                next();
              }
            })
            .catch((ex) => {
              reject();
            });
        }
        next();
      });
      return p;
    },
    getTotalRowsNumber() {
      let total = 0,
        breakLineNum = 0;
      _.each(this.searchItemSetting, (el, i) => {
        total += ~~el.layout;
        if (el.isLineBreak && i < this.searchItemSetting.length - 2) {
          breakLineNum++;
        }
      });
      let row_num = Math.ceil(total / 24);
      return row_num + breakLineNum;
    },
    //关闭检索项展开状态
    collapseItems(exclude) {
      _.each(this.searchItems, (item) => {
        if (exclude !== item) {
          item.isExtend = true;
          item.multiple = false;
          item.isExtend = false;
          item.tileMultipleState = false;
        }
      });
    },
    setLayout() {
      let matrix = [];
      let count = 0;
      let rowNum = 0;
      _.each(this.searchItems, (el, i) => {
        if (!matrix[rowNum]) {
          matrix[rowNum] = [];
        }
        if (count < 24) {
          matrix[rowNum].push(el);
          count += ~~el.layout;
        }
        let next = i + 1;
        if (this.searchItems[next]) {
          let temp = count + ~~this.searchItems[next].layout;
          if (temp > 24 || el.isLineBreak) {
            rowNum++;
            count = 0;
          }
        }
      });
      this.layout = matrix;
    },
    valueChange(evt, item) {
      this.postSearch();
    },
    emitValue() {
      let value = {};
      _.each(this.searchItems, (el) => {
        if (el.type == "datetime") {
          let baseformat = "yyyy-mm-dd";
          if (el.operator == "between") {
            let arr = dsf.type(el.values) == "array" ? el.values : [el.values];
            arr = _.filter(arr, (v) => v);
            value[el.field] = _.map(arr, (v, i) => {
              if (el.showModel == "ym") {
                if (i == 1) {
                  let m = v.getMonth() + 1;
                  let y = v.getFullYear();
                  let d = new Date(y, m, 0);
                  //将结束日期格式化到月最后一天
                  return dsf.date.format(d, baseformat);
                }
              }
              return dsf.date.format(v, baseformat);
            });
          } else if (el.operator == "gt" || el.operator == "lteq") {
            let val = dsf.type(el.values) == "array" ? el.values[0] : el.values;
            if (el.showModel == "ym") {
              if (val) {
                let m = val.getMonth() + 1;
                let y = val.getFullYear();
                let d = new Date(y, m, 0);
                value[el.field] = val ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = val ? dsf.date.format(val, baseformat) : "";
            }
          } else if (el.operator == "gteq" || el.operator == "lt") {
            if (el.showModel == "ym") {
              if (el.values) {
                let m = el.values.getMonth();
                let y = el.values.getFullYear();
                let d = new Date(y, m, 1);
                value[el.field] = el.values ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = el.values ? dsf.date.format(el.values, baseformat) : "";
            }
          } else if (el.operator == "eq") {
            let val = dsf.type(el.values) == "array" ? el.values[0] : el.values;
            if (el.showModel == "ym") {
              if (val) {
                let m = val.getMonth() + 1;
                let y = val.getFullYear();
                let d = new Date(y, m, 0);
                value[el.field] = val ? dsf.date.format(d, baseformat) : "";
              } else {
                value[el.field] = "";
              }
            } else {
              value[el.field] = val ? dsf.date.format(val, baseformat) : "";
            }
          }
        } else if (el.type == "number") {
          if (el.operator == "between") {
            if (el.values[0] == dsf.enums.numberBoundary.MIN && el.values[1] == dsf.enums.numberBoundary.MAX) {
              value[el.field] = null;
            } else {
              value[el.field] = el.values;
            }
          } else {
            value[el.field] = el.values;
          }
        } else if (el.type == "enum") {
          if (el.operator == "between") {
            if (el.customerBetween.length > 0 && el.customerBetween[0] && el.customerBetween[1]) {
              value[el.field] = el.customerBetween;
            } else {
              let arr = el.values[0] ? el.values[0].split(";") : "";
              if (arr.length == 2) {
                value[el.field] = arr;
              } else if (arr.length == 1) {
                value[el.field] = [arr[0], arr[0]];
              } else {
                value[el.field] = [];
              }
            }
          } else if (el.operator == "like" || el.operator == "slinke" || el.operator == "elike") {
            let arr = dsf.type(el.values) == "array" ? el.values : [el.values];
            value[el.field] = arr.join(" ");
          } else {
            let arr = dsf.type(el.values) == "array" ? el.values : [el.values];
            let result = _.filter(arr, (v) => {
              return dsf.isDef(v);
            });
            value[el.field] = result;
          }
          // if (el.showModel == 'treeselect') {
          //   let treePath = value[el.field];
          //   value[el.field] = _.map(treePath, (el) => {
          //     return el[el.length - 1];
          //   })
          // }
        } else {
          value[el.field] = el.values;
        }
      });
      for (let k in value) {
        if (dsf.isEmptyObject(value[k])) {
          delete value[k];
        }
      }
      return value;
    },
    postSearch(istrigger) {
      let value = this.emitValue();
      // this.$emit("update:queryValue", value);
      this.$nextTick(() => {
        this.$emit("change", value, istrigger);
      });
    },
    getValue() {
      return this.emitValue();
    },
    emitHeight() {
      let height = this.$el.offsetHeight;
      this.$emit("change-height", height);
    },
    resize() {
      if (this.loaded) {
        // debugger;
        _.each(this.searchItems, (el) => {
          let elm = this.$el.querySelector("[item-key='" + el.id + "']");
          if (elm && !el.isExtend) {
            let block = elm.querySelector(".ds-super-search-item-block");
            let optionbox = elm.querySelector(".ds-super-search-item-option");
            if (block && optionbox) {
              let blockHeight = block.offsetHeight;
              let optionHeight = optionbox.offsetHeight;
              if (optionHeight > blockHeight + 10) {
                el.isMore = true;
              } else {
                el.isMore = false;
              }
            }
          }
        });
        this.emitHeight();
      }
    },
    extendHandler() {
      this.closeOrOpen(!this.isExtend);
    },
    closeOrOpen(isExtend) {
      if (!this.owner.isDesign) {
        this.isExtend = isExtend;
        this.$emit("extend", this.isExtend);
        this.$nextTick(() => {
          if (!this.isExtend) {
            this.emitHeight();
          }
        });
      }
    }
  }
};
</script>
