<template>
  <div class="ds-login-page">
    <div class="login-header">
      <img ref="logo" v-if="logo" class="logo" :src="logo | imgFormat" :alt="title" />
      <div v-if="title" class="site-name">{{ title }}</div>
    </div>
    <div class="login-section" :style="{'background-image': 'url(' + backgroundImage + ')'}">
      <div class="login-box">
        <el-form @submit.native.prevent>
          <slot>
            <div class="login-box-title">登 录</div>
            <div class="login-box-info">
              <el-input v-model.trim="username" :disabled="loading" :placeholder="userNamePlaceholder" size="medium" prefix-icon="el-icon-user-solid">
              </el-input>
            </div>
            <div class="login-box-info">
              <el-input v-model.trim="password" :disabled="loading" type="password" :placeholder="passwordPlaceholder" size="medium" prefix-icon="el-icon-s-goods"></el-input>
            </div>
            <div class="login-box-button">
              <el-button :loading="loading" type="info" native-type="submit" size="small" @click="submit">登 录</el-button>
            </div>
            <div class="login-box-forgot-password" v-if="isForgotPassword">
              <span @click="toForgotPassword">忘记密码?</span>
            </div>
          </slot>
        </el-form>
      </div>
    </div>
    <div class="login-footer"></div>
  </div>
</template>

<script>
export default {
  name: "DsfPlatformLogin",
  data() {
    return {
      loading: false,
      username: "",
      password: ""
    };
  },
  props: {
    logo: {
      type: String,
      default() {
        return dsf.config.setting_public_login_logo;
      }
    },
    title: {
      type: String,
      default() {
        return dsf.config.setting_public_login_title;
      }
    },
    isForgotPassword:{
      type:Boolean,
      default:false
    },
    login: {
      type: Function,
      default: null
    },
    userNamePlaceholder: {
      type: String,
      default: "账户名称"
    },
    passwordPlaceholder: {
      type: String,
      default: "密码"
    },
    backgroundImage: {
      type: String,
      default() {
        return dsf.config.setting_public_login_bg || require("../../assets/styles/themes/img/loginBg.png");
      }
    }
  },
  methods: {
    toForgotPassword(){
      // location.replace(dsf.config.setting_public_forgot_password_page)
      location.href=dsf.config.setting_public_forgot_password_page
    },
    submit() {
      if (!this.login) {
        this.username = _.trim(this.username);
        this.password = _.trim(this.password);
        if (this.username === "") {
          dsf.layer.message("账号不能为空！", false);
          return;
        }
        if (this.password === "") {
          dsf.layer.message("密码不能为空！", false);
          return;
        }
        this.loginHandler();
      } else if (dsf.type(this.login) == "function") {
        this.login({
          username: this.username,
          password: this.password
        }).then((res) => {
          this.setStateIsLogin();
        });
      }
    },
    loginHandler() {
      this.loading = true;
      this.$webAPI
        .login({
          userName: this.username,
          password: dsf.md5(dsf.md5(this.password)),
          code: "good"
        })
        .then(({ success, message }) => {
          if (success) {
            this.setStateIsLogin();
          } else {
            dsf.layer.message(message, false);
          }
        })
        .catch((err) => {
          if (err) {
            dsf.layer.message(err.message || "未知错误", false);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    // 储存cookie登录状态
    setStateIsLogin() {
      dsf.cookies.set("isLogin", 1);
      let from = this.$route.query.from;
      if (from) {
        from = decodeURIComponent(from);
      } else {
        from = dsf.config.setting_public_login_success_to;
      }
      location.replace(from);
    }
  }
};
</script>