import { DsfHomeTabPanel } from './tabPanel';
import { DsfVirtualList } from './virtualList';
import { DsfHomePanel } from './panel';
import { DsfHomeUserCenter } from './userCenter';
import { DsfUniteHomeAnnouncemenMail } from './announcemenMail';
import { DsfPlatformFixedMenu } from './fixedMenu';
import { DsfMessageNotice } from './messageNotice';
import { DsfInternalMail } from './internalMail';
import { DsfShortcutMenu } from './shortcutMenu';
import { DsfMessageNotification } from './messageNotification';
import { DsfScheduleList } from './scheduleList';
import { DsfHomePendingProcess } from './pendingProcess';
import { DsfScheduleListItem } from './scheduleListItem';
import { DsfNavMenuSetting } from './navMenuSetting';

function install(Vue) {
  window.Vue = Vue;
  Vue.component(DsfVirtualList.name, DsfVirtualList);
  Vue.component(DsfHomeTabPanel.name, DsfHomeTabPanel);
  Vue.component(DsfHomePanel.name, DsfHomePanel);
  Vue.component(DsfHomeUserCenter.name, DsfHomeUserCenter);
  Vue.component(DsfUniteHomeAnnouncemenMail.name, DsfUniteHomeAnnouncemenMail);
  Vue.component(DsfPlatformFixedMenu.name, DsfPlatformFixedMenu);
  Vue.component(DsfShortcutMenu.name, DsfShortcutMenu);
  Vue.component(DsfMessageNotice.name, DsfMessageNotice);
  Vue.component(DsfInternalMail.name, DsfInternalMail);
  Vue.component(DsfMessageNotification.name, DsfMessageNotification);
  Vue.component(DsfScheduleList.name, DsfScheduleList);
  Vue.component(DsfHomePendingProcess.name, DsfHomePendingProcess);
  Vue.component(DsfScheduleListItem.name, DsfScheduleListItem);
  Vue.component(DsfNavMenuSetting.name, DsfNavMenuSetting);
}

export {
  install,
  DsfHomeTabPanel,
  DsfUniteHomeAnnouncemenMail,
  DsfHomePanel,
  DsfHomeUserCenter,
  DsfPlatformFixedMenu,
  DsfMessageNotice,
  DsfInternalMail,
  DsfShortcutMenu,
  DsfMessageNotification,
  DsfScheduleList,
  DsfHomePendingProcess,
  DsfScheduleListItem,
  DsfNavMenuSetting
};

export default install