<template>
  <div class="dsf-dingtalk-home-aside-menu">
    <el-menu unique-opened>
      <template v-for="menu in menuList">
        <home-menu-group v-if="menu.children && menu.children.length" :key="menu.menu_id" :menu="menu" />
        <el-menu-item v-else :key="menu.menu_id" :index="menu.menu_id" @click="dingRoot.menuItemClick(menu)">
          <i v-if="menu.menu_icon" :class="[menu.menu_icon]" class="iconfont"></i>
          <span>{{ menu.menu_name }}</span>
        </el-menu-item>
      </template>
    </el-menu>
  </div>
</template>

<script>
import homeMenuGroup from './menu/homeMenuGroup';

export default {
  name: "HomeAsideMenu",
  inject: ['homeRoot', 'dingRoot'],
  components: {
    homeMenuGroup
  },
  computed: {
    menuList() {
      return this.homeRoot.menuList || [];
    }
  }
}
</script>