<template>
  <div v-loading="loading" class="dsf-control ds-schedule">
    <div v-if="classInfo" class="className">{{ classInfo.name || "" }}</div>
    <dsf-calendar-nav
      :is-design="isDesign"
      :url="url1"
      :data-loaded-fun="dataLoadedFun"
      @change="scheduleChange"
      :isparameter="isparameter"
    ></dsf-calendar-nav>
    <dsf-virtual-scroll scroll-Y>
      <div v-if="!list.length" class="no-data">没有数据啦~</div>
      <div
        v-for="(item, index) in list"
        :key="index"
        class="ds-schedule-item"
      >
        <div class="ds-schedule-item-title">
          {{ item.theme || "" }}
        </div>
        <div class="ds-schedule-item-info">
          <span class="ds-schedule-item-time">
            {{ item.startTime || "" }} - {{ item.endTime || "" }}
          </span>
          <span v-if="!classInfo" class="ds-schedule-item-className">
            {{ item.addressText || "" }}
          </span>
        </div>
        <!-- <div class="ds-teas-unite-schedule-item-info">
          <span class="ds-teas-unite-schedule-item-teacher">{{
            item.teacher
          }}</span>
          <span
            v-if="item.addressText && item.addressText != ''"
            class="ds-teas-unite-schedule-item-classroom"
          >
            <i class="iconfont icon-ditu-dibiao"></i>
            {{ item.addressText }}
          </span>
        </div> -->
      </div>
    </dsf-virtual-scroll>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfScheduleListItem",
  mixins: [$mixins.control],
  ctrlCaption: "日程列表组件",
  design: {
    isMask: false,
  },
  props: {
    url: {
      type: String,
      default: null,
    },
    classInfo: {
      type: [Object, String],
      default: null,
    },
    isparameter: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // 请求次数
      postNum: 0,
      loading: false,
      list: [],
      url2: "",
    };
  },
  computed: {
    classIdParam() {
      return this.classInfo ? "?classId=" + this.classInfo.id : "";
    },
    url1() {
      if (!this.url || this.url == "") {
        return null;
      }
      //   return this.url + this.classIdParam;
      return '/calendar/info/queryUserCalendarNumByDate';
    },
  },
  created() {
    if (this.isDesign) {
      for (var i = 0; i < 10; i++) {
        this.list.push({
          courseName: "20200118jinhy排课测试课程",
          className: "fuxm外培测试新新新",
          courseTime: "24日14:30-24日17:00",
          teacher: "金皓宇",
          classroom: "知新101",
        });
      }
    }
  },
  mounted() {
    if (this.isDesign) {
      this.$dispatch("design-height-change", "100%");
    }
  },
  methods: {
    reloadData() {
      this.postData();
    },
    dataLoadedFun(data, vm) {
      let _this = this;
      let days = [];
      _.forEach(data, (n, d) => {
        if (n > 0) {
          days.push(d);
        }
      });
      days.sort();
      if (this.postNum > 1) {
        changeActive();
        return;
      }
      let tabCtrl = fn(this.$parent);
      if (!tabCtrl?.slots) {
        return;
      }
      let tabIndex = fnTabIndex(tabCtrl.slots || []);
      let tabName = tabIndex > -1 ? tabCtrl.slots[tabIndex].name : null;
      // 此页无数据
      if (!days.length) {
        // console.log(tabCtrl.active, tabCtrl.slots, tabCtrl, this.caption);
        if (tabIndex > -1) {
          tabCtrl.slots[tabIndex].hasData = false;
          this.$nextTick(() => {
            // 如果恰好此页无数据
            if (tabCtrl.active === tabName) {
              let validTab = tabCtrl.slots.find((s) => s.hasData);
              if (validTab) {
                tabCtrl.tabClick(validTab);
              }
            }
          });
        }
        return;
      }
      changeActive();
      if (tabIndex > -1) {
        tabCtrl.slots[tabIndex].hasData = true;
      }
      // 如果不是此页
      if (tabCtrl.active !== tabName) {
        this.$nextTick(() => {
          let activeTab = tabCtrl.slots.find((s) => s.name === tabCtrl.active);
          if (activeTab.hasData === false) {
            let validTab = tabCtrl.slots.find((s) => s.hasData);
            if (validTab) {
              tabCtrl.tabClick(validTab);
            }
          }
        });
      }

      // 切换到有效的日期去
      function changeActive() {
        // 此页有数据
        if (!data[vm.active.id]) {
          let active = vm.weeks.find((w) => w.id === days[0]);
          if (active) {
            vm.active = active;
          }
        }
      }
      // 找到tab容器
      function fn(vvm) {
        if (vvm.$parent.ctrlName === "DsfHomeTabPanel") {
          return vvm.$parent;
        }
        if (!vvm.$parent || vvm.$parent === _this.$root) {
          return null;
        }
        return fn(vvm.$parent);
      }
      // 找到所在tab索引
      function fnTabIndex(slots) {
        return slots.findIndex((s) => {
          if (!s.controls) return false;
          return s.controls.find((c) => c.caption === _this.caption);
        });
      }
    },
    scheduleChange({ args: { id } }) {
      if (!this.url || this.url == "") {
        this.url2 = null;
      } else {
        this.url2 =
          this.url + "?startDate=" + id + "&endDate=" + id + this.classIdParam;
      }
      this.postData();
    },
    postData() {
      if (this.isDesign) return;
      this.postNum++;
      if (!this.url2 || this.url2 == "") {
        this.list = [];
        return;
      }
      this.loading = true;
      this.$http
        .get(this.url2)
        .done(({ success, data, message }) => {
          if (success) {
            let list = Object.values(data);
            this.list = list.length ? list[0] : [];
          } else {
            dsf.layer.message(message, false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err.message || "请求异常", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
});
</script>
