<template>
  <el-cascader
    ref="cascader"
    class="ds-area-select-cascader"
    v-model="value"
    :key="thisKey"
    :props="options"
    :options="items"
  />
</template>

<script>
import { getOptions } from './utils/areaUtils';

export default {
  name: 'DsfAreaCascader',
  props: {
    isDesign: {
      type: Boolean,
      default: false
    },
    rangeStart: {
      type: Number,
      default: 0
    },
    rangeEnd: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      isInit: false,
      thisKey: dsf.uuid(8),
      options: {
        value: 'dsfa_area_id',
        label: 'name',
        children: 'children',
        lazy: true,
        lazyLoad: this.getData
      },
      items: []
    }
  },
  computed: {
    value: {
      get () {
        let [a, ...b] = this.$parent.ids;
        this.updateKey(b);
        return b;
      },
      set (to) {
        let res = null;
        if (to?.length) {
          let panel = this.$refs.cascader.$refs.panel;
          let lastId = to[to.length - 1];
          let text = [];
          _.each(panel.activePath, (p, i) => {
            text.push(p.label);
            if (i == panel.activePath.length - 1) {
              let c = p.children.find(c => lastId === c.value);
              text.push(c.label);
            }
          });
          res = {
            text: text.join('-'),
            value: lastId
          };
        }
        this.valueCopy = to.join('.');
        this.$parent.emitValueChange(res);
        this.$parent.$nextTick(() => {
          this.$parent.emitFormValidate(res);
        });
      }
    }
  },
  methods: {
    updateKey(b) {
      let bStr = b.join('.');
      // 判断如果由外部赋值，更新key可触发选项更新
      if (this.valueCopy !== bStr) {
        this.valueCopy = bStr;
        this.thisKey = dsf.uuid(8);
      }
      this.$nextTick(() => {
        if (b?.length) {
          let inputValue = this.$refs.cascader.inputValue;
          if (!inputValue) {
            this.$refs.cascader.inputValue = this.$parent.value.text.replace(/-/g, ' / ');
          }
        }
      })
    },
    getData(node, resolve) {
      if (this.isDesign) {
        resolve([]);
        this.isInit = true;
        return;
      }
      let { root, data } = node;
      let ids, level;
      if (root) {
        ids = this.$parent.superCode;
        level = this.rangeStart + 1;
      } else {
        let { treeinfo_globalid, treeinfo_level } = data;
        ids = treeinfo_globalid;
        level = treeinfo_level;
      }
      if (level > this.rangeEnd + 1) {
        resolve([]);
        this.isInit = true;
        return;
      }
      getOptions.call(this, {
        ids,
        endRange: level +1,
        startRange: level + 1
      }, list => {
        if (level === this.rangeEnd + 1) {
          list.forEach(l => l.leaf = true);
        }
        this.isInit = true;
        resolve(list);
      });
    }
  }
};
</script>