<template>
  <!--设计器状态 start-->
  <div tabindex="-1" v-if="$getComponetRenderType('design-proxy')" v-show="__desContext__.show" :class="{ 'ds-control-selected': __desContext__.selected }" :style="{ height: __desContext__.height }" class="designer-ctrl-proxy">
    <component
      :key="'ctrl'"
      :is="__context__.props.ctrlName"
      v-if="!$data.$isReload"
      :ctrl-index="ctrlIndex"
      ref="ctrl"
      :is-design="__hasDesign__"
      v-bind.sync="__context__.props"
      @design-height-change="designHeightChange"
      :static-value.sync="$designer.staticData[__context__.props.caption]">
      <template v-for="slot in __context__.props.slots" v-slot:[slot.name]>
        <DsfLayoutTree v-for="(child,index) in slot.controls" :key="child.designId" :ctrl-index="index" :component-props="child" :is-design="__hasDesign__" :parent-prop="_self" :root-prop="__context__.root"></DsfLayoutTree>
      </template>
    </component>
    <!--组件设计模式左上角按钮-->
    <div :key="'mask'" :class="{ 'designer-mask': __desContext__.mask }" class="designer-btns">
      <ul v-show="__desContext__.selected">
        <li :key="'info'" v-if="__desContext__.hasInfo" class="info" title="元数据详情" @click.stop="$detail()" @mousedown.stop>
          <DsfIcon :style="{ color: '#1E9FFF' }" name="fangdajing" />
        </li>
        <li :key="'doc'" v-show="hasComponentDocument" class="componentdoc" title="描述文档" @click.stop="$viewDoc()" @mousedown.stop>
          <DsfIcon :style="{ color: '#1E9FFF' }" name="icon_bangzhuwendang" />
        </li>
        <li :key="'dragdrap'" v-if="__desContext__.hasDragDrop" ref="dragdrap" class="dragdrop" title="拖动" @mousedown.stop>
          <DsfIcon :style="{ color: '#FFB800' }" name="yidong" />
        </li>
        <template>
          <li :key="'setting'" v-if="__desContext__.hasSetting&&(settingMenus&&settingMenus.length>0)" class="setting" title="设置" @mousedown.stop>
            <el-dropdown placement="bottom" :hide-on-click="false" @command="$settingMenuItemClickHandler">
              <DsfIcon :style="{ color: '#1E9FFF' }" name="shezhi" />
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item :command="item.command" v-for="(item,$index) in settingMenus" :key="$index">
                  {{item.text}}
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </li>
          <li :key="'setting'" v-else-if="__desContext__.hasSetting&&(__desContext__.settingButtonHandler)" @click.stop.prevent="$setting()">
            <DsfIcon slot="reference" :style="{ color: '#1E9FFF' }" name="shezhi" />
          </li>
        </template>
        <li :key="'remove'" v-if="__desContext__.hasRemove" class="remove" title="移除" @click.stop.prevent="$remove()" @mousedown.stop>
          <DsfIcon :style="{ color: '#FF0000' }" name="shanchu" />
        </li>
      </ul>
      <div :key="'code'" v-if="__context__.props.isFormControl" :title="__context__.props.caption" class="designer-code">
        {{ __context__.props.caption }}
      </div>
    </div>
  </div>
  <!--设计器状态内置组件，比如table中必须是tr不能用代理的div绘制-->
  <component :is="__context__.props.ctrlName" v-else-if="$getComponetRenderType('design-no-proxy')" :ctrl-index="ctrlIndex" v-show="__desContext__.show" ref="ctrl" :is-design="__hasDesign__" v-bind.sync="__context__.props" :static-value.sync="$designer.staticData[__context__.props.caption]">
    <template v-for="slot in __context__.props.slots" v-slot:[slot.name]>
      <DsfLayoutTree v-for="(child,index) in slot.controls" :key="child.designId" :ctrl-index="index" :component-props="child" :is-design="__hasDesign__" :parent-prop="_self" :root-prop="__context__.root"></DsfLayoutTree>
    </template>
  </component>
  <!--设计器状态 end-->
</template>
<script>
let dicts = {};
/* eslint-disable vue/prop-name-casing */
export default {
  name: "DsfLayoutTree",
  inheritAttrs: false,
  provide() {
    return {
      $layoutParent: this,
      $layoutTree: this
    };
  },
  inject: {
    $layoutParent: {
      default: null
    },
    $designer: {
      default: null
    },
    $list: {
      default: null
    }
  },
  props: {
    //组件属性
    componentProps: {
      type: Object,
      default() {
        return null;
      }
    },
    //根组件
    rootProp: {
      type: Object,
      default() {
        return null;
      }
    },
    //父组件
    parentProp: {
      type: Object,
      default() {
        return null;
      }
    },
    ctrlIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      $layoutContext: {
        props: null,
        root: null,
        parent: null,
        controls: {}
      },
      $designContext: {
        show: true,
        height: "auto",
        width: "auto",
        mask: false,
        hasInfo: false,
        hasDragDrop: true,
        hasRemove: true,
        hasSetting: true,
        selected: false,
        settingButton: false,
        settingMenus: [],
        settingButtonHandler: null
      },
      $events: {},
      $clientId: "",
      $scopeData: null,
      $isReload: false
    };
  },
  computed: {
    __context__() {
      return this.$data.$layoutContext;
    },
    __desContext__() {
      return this.$data.$designContext;
    },
    __hasDesign__() {
      return this.$attrs["is-design"];
    },
    __nodeSwitch__() {
      let isDesignProxy = this.__context__.props.designProxy;
      return isDesignProxy ? "design-proxy" : "design-no-proxy";
    },
    hasComponentDocument() {
      const doc = this.$designer?.pageDocument;
      const id = this.__context__.props.designId;
      if (doc && id) {
        return new RegExp(`did="${id}"`, "gi").test(doc);
      }
      return false;
    },
    slots() {
      return this?.__context__?.props?.slots || [];
    },
    settingMenus() {
      let items = this.__desContext__.settingMenus;
      let showItems= _.filter(items, (it) => {
        let result = this.$settingMenuItemShow(it);
        return result;
      });
      return showItems;
    }
  },
  watch: {
    //监听枚举类型的元数据中选项数据变化
    "__context__.props.metadata.dataSource": {
      handler(v) {
        if (this.__context__.props.metadata.ckey == "items-meta-data" || this.__context__.props.metadata.dataSourceWatch) {
          //枚举类型数据源请求
          setEnumItems.call(this, v);
        }
      }
    }
  },
  created() {
    this.__context__.props = this.componentProps;
    if (this.__context__.props.isView) {
      this.__context__.root = this;
    } else {
      this.__context__.root = this.rootProp;
    }
    if (this?.__context__?.props?.isCopy) {
      this.$updateCopyComponentNewCaption();
      this.$nextTick(() => {
        this.$updateCopyComponentStaticValue();
      });
      delete this.__context__.props.isCopy;
    }
  },
  updated() {
    // if (this.$refs.ctrl) {
    // dsf.warn(this.__context__.props.ctrlName + "被更新")
    // }
  },
  mounted() {
    // this.$refs.ctrl.$on('height.change', (evt) => {
    //   this.__desContext__.height = evt.args;
    // });
    // dsf.time(++count)
    designInit.call(this);
    if (!this?.$refs?.ctrl?.$options) {
      dsf.layer.message(`【${this.__context__.props.ctrlName}】组件没有被注册，请调用Vue.component函数进行全局注册`, false);
      return;
    }
    // else {
    //   console.log(this.__context__.props.ctrlName);
    // }
    createPropsProxy.call(this);
    createComputedProxy.call(this);
    createMethodsProxy.call(this);
  },
  destroyed() {
    // console.log("销毁组件" + this.__context__.props.ctrlName)
    destroyPropsProxy.call(this);
    destroyComputedProxy.call(this);
    destroyMethodsProxy.call(this);
    this.__context__.props = null;
  },
  methods: {
    designHeightChange(v) {
      this.$data.$designContext.height = v.args;
      // this.__desContext__.height = v;
    },
    $getComponetRenderType(type) {
      return this.__nodeSwitch__ == type && this.__context__.props;
    },
    //获取真实组件
    $$getComponent() {
      return this.$refs.ctrl;
    },
    $lockAnUnLock() {
      this.__context__.props.designLock = !this.__context__.props.designLock;
    },
    //移除组件
    $remove() {
      //移除组件
      let target = $(this.$el);
      let slotEl = $(target).closest("[slot-name]");
      let parent = dsf.designer.getParentControl(target);
      let slotName = slotEl.attr("slot-name");
      // if (this.$$getComponent().isQuestionControl) {
      //   let nextEles = $(this.$$getComponent().$el).parent().nextAll().find(".ds-question-item"),
      //     nexts = [];
      //   window._.forEach(nextEles, function (item) {
      //     nexts.push(item.__vue__);
      //   });
      //   this.rootProp.$parent.setNextQuestionIndex(this.$$getComponent().questionIndex, nexts);
      // }
      if (parent) {
        let realControl = parent.$$getComponent ? parent.$$getComponent() : parent;
        let slots = !realControl.slots ? realControl.slots : realControl.slots || [];
        let isDataGridCell = realControl.$el.matches(".ds-datagrid-cell-content");
        let slotIndex = _.findIndex(slots, (s) => s.name == slotName);
        if (slots[slotIndex]) {
          dsf.array.remove(slots[slotIndex].controls, this.__context__.props);
        }
        if (isDataGridCell) {
          if (slots[0].controls.length > 0) {
            let child = slots[0].controls[0];
            parent.updateColumn(child);
          } else {
            parent.clearColumn();
          }
        }
      }
    },
    //设置组件
    $setting() {
      let designOptions = dsf.getComponentDesignOptions(this.__context__.props.ctrlType);
      // this.$bus.$emit(`questionSetting`);
      if (designOptions) {
        designOptions.settingButtonHandler.call(this.$refs.ctrl);
      }
    },
    $settingMenuItemClickHandler(command) {
      let item = _.find(this.__desContext__.settingMenus || [], (it) => {
        return it.command === command;
      });
      if (item) {
        item.handler.call(this.$$getComponent(), item, this);
      }
    },
    $settingMenuItemShow(item) {
      if (item.hidden) {
        let result = item.hidden.call(this.$$getComponent());
        if (result === true) {
          return false;
        }
      }
      return true;
    },
    $viewDoc() {
      this.$bus.$emit(`showComponentDoc`, {
        id: this.$$getComponent()?.$options?.propsData?.designId
      });
    },
    $getRoot() {
      let root = this.rootProp;
      if (!root && this.__context__.props.isView) {
        root = this;
      }
      return root;
    },
    $detail() {
      try {
        const component = this.$$getComponent();
        const info = {
          caption: component.$options.ctrlCaption,
          id: component.metadata.id,
          name: component.metadata.name,
          typeText: component.metadata.type?.text,
          typeValue: component.metadata.type?.value
        };
        dsf.layer.openDialog({
          title: "控件信息",
          fullscreen: false,
          content: "DsfComponentInfo",
          params: {
            info: info
          },
          width: "350px",
          height: "150px"
        });
      } catch (ex) {
        dsf.layer.alert("请保存后再试");
      }
    },
    $updateCopyComponentNewCaption() {
      let ctrl = this.__context__.props;
      let desId = ctrl.designId;
      let serial = this.$designer.$refs.viewProxy.getSerial();
      let coms = this.$designer.getComponentsByNode();
      coms = _.filter(coms, (it) => it.designId != ctrl.designId);
      let num = ctrl.caption.replace(/\D/g, "");
      num = parseInt(num) || 0;
      let max = 100,
        currIndex = 0;
      let isChangeCaption = false;
      while (max > currIndex) {
        let index = _.findIndex(coms, (it) => {
          return it.caption == ctrl.caption && it.designId != ctrl.designId;
        });
        if (index < 0) {
          break;
        } else {
          ctrl.caption = ctrl.caption.replace(/\d/g, "") + ++num;
          isChangeCaption = true;
        }
        currIndex++;
      }
      num = 0;

      // ctrl.staticValue = null;
      // captions.push(ctrl.caption);
      if (this.__context__.props.metadata) {
        //如果再列表和子表中
        if (this.$list && this.$list.isOneToMany) {
          //如果是子表,将元数据重置
          delete this.__context__.props.metadata.isFromMetaData;
        } else if (this.$list) {
          //如果再datagrid中目前无特殊操作，后续如果有可以在这里补充
        } else {
          //普通表单组件直接重置元数据
          delete this.__context__.props.metadata.isFromMetaData;
        }
      }
      return isChangeCaption;
    },
    $updateCopyComponentStaticValue() {
      try {
        //对带有子元数据的静态数据做一次值修复
        if ("childMetaDataCodes" in this.__context__.props) {
          let oldKeys = this.__context__.props.childMetaDataCodes.slice(0);
          dsf.designer.createComponentMetaData(this, this.$designer.$refs.viewProxy);
          let data = [];
          let newKeys = this.__context__.props.childMetaDataCodes.slice(0);
          _.each(this.staticValue || [], (row) => {
            let newRow = {};
            _.each(newKeys, (key, i) => {
              newRow[key] = row[oldKeys[i]];
            });
            data.push(newRow);
          });
          if (data.length > 0) {
            this.staticValue = data;
          } else {
            this.staticValue = null;
          }
        }
      } catch (ex) {
        this.staticValue = null;
      }
    }
  }
};

//枚举类型数据源请求
function setEnumItems(v) {
  if (v.type == "static") {
    if (dsf.isDef(v.code)) {
      this.__context__.props.items = v.code;
    }
  } else if (v.type == "dict") {
    if (!dicts[v.code]) {
      this.$http
        .get("dict/getList/" + v.code)
        .done((result) => {
          this.__context__.props.items = result.data;
          dicts[v.code] = result.data;
        })
        .catch(() => {});
    } else {
      this.__context__.props.items = dsf.mix([], dicts[v.code]);
    }
  } else if (v.type == "db") {
    if (!dicts[v.code]) {
      this.$http
        .get("meta/tree/dbSource?rmId=" + v.code)
        .done((result) => {
          this.__context__.props.items = result.data;
          dicts[v.code] = result.data;
        })
        .catch(() => {});
    } else {
      this.__context__.props.items = dsf.mix([], dicts[v.code]);
    }
  }
}

function designInit() {
  if (!this.$attrs["is-design"]) {
    return;
  }
  initDragDrop.call(this);
  // let config = this.__context__.props. || { markObserver: false, allowAddChild: false };
  this.__desContext__.mask = true;
  this.__desContext__.hasInfo = false;
  this.__desContext__.hasDragDrop = true;
  this.__desContext__.hasRemove = true;
  this.__desContext__.hasSetting = false;
  let designOptions = dsf.getComponentDesignOptions(this.__context__.props.ctrlType);
  if (designOptions) {
    this.__desContext__.mask = designOptions.isMask;
    this.__desContext__.hasInfo = designOptions.infoButton;
    this.__desContext__.hasDragDrop = designOptions.dragButton;
    this.__desContext__.hasRemove = designOptions.removeButton;
    this.__desContext__.hasSetting = designOptions.settingButton;
    this.__desContext__.settingMenus = designOptions.settingMenus;
    this.__desContext__.settingButtonHandler = designOptions.settingButtonHandler;
    if (designOptions.fit) {
      this.__desContext__.height = "100%";
    } else {
      this.__desContext__.height = this.__desContext__.height != "auto" ? this.__desContext__.height : "auto";
    }
  }
}

function initDragDrop() {
  //附加工具箱拖动
  if (this.$refs.dragdrap) {
    $(this.$refs.dragdrap).draggable({
      addClasses: false,
      appendTo: ".ds-designer",
      distance: 10,
      revert: "invalid",
      cursor: "default",
      containment: "window",
      cursorAt: {
        left: -20,
        top: -20
      },
      helper: () => {
        let proxy = $("<div class='ds-tool-item proxy'>" + this.__context__.props.caption + "</div>");
        proxy.data("data", {
          state: "move",
          attributes: this.__context__.props,
          component: this
        });
        return proxy;
      },
      start: () => {
        this.__desContext__.show = false;
      },
      stop: () => {
        this.__desContext__.show = true;
      }
    });
  }
}

//代理属性
function createPropsProxy() {
  // 代理组件属性
  let propsKey = Object.keys(this.$props);
  for (let k in this.__context__.props) {
    if (propsKey.indexOf(k) < 0) {
      Object.defineProperty(this, k, {
        configurable: true,
        set(v) {
          this.__context__.props[k] = v;
        },
        get() {
          return this.__context__.props[k];
        }
      });
    } else {
      throw this.$refs.ctrl.ctrlName + '包含了与代理组件一样的属性"' + k + '"';
    }
  }
}
//代理计算属性
function createComputedProxy() {
  //代理计算属性
  if (this.$refs.ctrl && this.$refs.ctrl.$options.computed) {
    //代理计算属性
    for (let k in this.$refs.ctrl.$options.computed) {
      Object.defineProperty(this, k, {
        configurable: true,
        set(v) {
          this.$refs.ctrl[k] = v;
        },
        get() {
          return this.$refs.ctrl[k];
        }
      });
    }
  }
}
//代理组件api
function createMethodsProxy() {
  //代理方法
  if (this.$refs.ctrl && this.$refs.ctrl.$options.methods) {
    for (let k in this.$refs.ctrl.$options.methods) {
      this[k] = this.$refs.ctrl[k].bind(this.$refs.ctrl);
    }
  }
}

function destroyPropsProxy() {
  // 代理组件属性
  let propsKey = Object.keys(this.$props);
  for (let k in this.__context__.props) {
    if (propsKey.indexOf(k) < 0) {
      delete this[k];
    }
  }
}

function destroyComputedProxy() {
  //代理计算属性
  if (this.$refs.ctrl && this.$refs.ctrl.$options.computed) {
    //代理计算属性
    for (let k in this.$refs.ctrl.$options.computed) {
      delete this[k];
    }
  }
}

function destroyMethodsProxy() {
  if (this.$refs.ctrl && this.$refs.ctrl.$options.methods) {
    for (let k in this.$refs.ctrl.$options.methods) {
      this[k] = null;
      delete this[k];
    }
  }
}

//是否是表格结构的数据
function isChildScope(scope) {
  if (dsf.isDef(scope.row) || dsf.isDef(scope.$index) || dsf.isDef(scope.column)) {
    return true;
  }
  return false;
}
</script>
