<template>
  <div class="ds-control ds-calendar-nav">
    <div class="ds-calendar-nav-body">
      <div class="ds-calendar-nav-body-header">
        <span>周一</span>
        <span>周二</span>
        <span>周三</span>
        <span>周四</span>
        <span>周五</span>
        <span>周六</span>
        <span>周日</span>
      </div>
      <div class="ds-calendar-nav-body-content">
        <div class="ds-calendar-nav-body-row">
          <template v-for="day in weeks">
            <div :key="day.id" class="ds-calendar-nav-body-cell">
              <div
                class="ds-calendar-nav-body-cell-bt"
                :class="{ active: active.id == day.id }"
                @click="active = day"
              >
                <span class="day">{{ day.day }}</span>
                <span class="zhDay">{{ day.lunarDay }}</span>
                <i
                  class="tips"
                  :class="{ hide: !isDesign && !scheduleInfo[day.id] }"
                ></i>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const oneDay = 86400000;

export default dsf.component({
  name: "DsfCalendarNav",
  mixins: [$mixins.control],
  ctrlCaption: "日历导航",
  design: {
    isMask: false,
  },
  props: {
    url: {
      type: String,
      default: "",
    },
    dataLoadedFun: {
      type: Function,
      required: false,
    },
    isparameter: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      active: null,
      weeks: [],
      // 日程信息
      scheduleInfo: {},
    };
  },
  watch: {
    active(to) {
      if (to) {
        this.$dispatch("change", to);
      }
    },
    url(to) {
      this.reloadData();
    },
  },
  created() {
    this.$http
      .importFiles(dsf.url.getWebPath("$/js/libs/lunar/lunarDay.js"))
      .then(([{ state }]) => {
        if (state) {
          this.reloadData();
        } else {
          console.error("加载lunarDay.js文件出错");
        }
      })
      .catch((err) => {
        console.error(err);
      });
  },
  methods: {
    reloadData() {
      if (!window.Solar) return;
      this.scheduleInfo = {};
      let { weeks, active } = this.getWeeks();
      this.weeks = weeks;
      this.active = active;
      let etime = dsf.date.format(
        new Date(weeks[weeks.length - 1].date.getTime() + oneDay),
        "yyyy-mm-dd"
      );
      this.isDesign || this.getScheduleInfo(weeks[0].id, etime);
    },
    getWeeks(day = dsf.date.toDay()) {
      let weeks = [];
      let week = new Date(day).getDay() || 7;
      let toDay = dsf.date.toDay(),
        active = null;
      for (let i = 1 - week, l = 7 - week; i <= l; i++) {
        let t = day + oneDay * i;
        let d = new Date(t);
        let lunar = window.Solar.fromDate(d);
        let item = {
          id: lunar.toYmd(),
          date: d,
          day: d.getDate(),
          lunarDay: lunar.getLunar().getDayInChinese(),
        };
        weeks.push(item);
        if (t == toDay) {
          active = item;
        }
      }
      if (!active) {
        active = weeks[0];
      }
      return { weeks, active };
    },
    // 获取日程信息
    getScheduleInfo(stime, etime) {
      if (!this.url || this.url == "") {
        this.scheduleInfo = {};
        return;
      }
      var reqData = {};
      // debugger;
      stime += ' 00:00:00';
      etime += ' 23:59:59';
      if (this.isparameter) {
        reqData = {
          startDate: stime,
          endDate: etime,
        };
      } else {
        reqData = {
          stime: stime,
          etime: etime,
        };
      }

      this.$http
        .get(this.url, reqData)
        .done(({ success, data, message }) => {
          if (success) {
            this.scheduleInfo = data;
            this.dataLoadedFun?.(data, this);
          } else {
            dsf.layer.message(message, false);
          }
        })
        .error((err) => {
          console.error(err);
          dsf.layer.message(err.message || "请求异常", false);
        });
    },
  },
});
</script>